@include media-breakpoint-up(lg) { 
    .twocolumncallouthomevideo{

    .row{
        padding:20px;
        display: flex!important;
        .text-area{
            float: left;
           
            padding-left: 0; 
            img{
                float:left;
                padding:20px;
              
            }
            h2{
                padding-bottom: 0;
                padding-top: 20px;
                font-size: 36px;
            }
            h3{
                color: $blue;
                font-size: 24px;
                padding-bottom: 10px;
                font-weight: $light;
            }
            p{
                
                font-weight: $light;
            }
            a{
                color: $green;
                font-size: 16px;
            }
        }
       
    }
}
}
