/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/

@media (min-width: 320px) and (max-width: 1024px) {

    header {
        height: 67.9px;
        width: 100%;
        float: left;
        left: 0;
        top: 0;
        z-index: 3;
        -webkit-transition: all ease .5s;
        -moz-transition: all ease .5s;
        -o-transition: all ease .5s;
        transition: all ease .5s;
        overflow: hidden;


        .container {
            padding-top: 9px;

            .branding {
                width: 60%;
                float: left;

                .logo {
                    &.mobile {
                        display: block;
                        padding-bottom: 10px!important;
                    }

                    &.web {
                        display: none;
                    }
                }
            }

            .mobile-nav {
                float: right;
                padding-top: 10px;

                > ul {
                    padding: 0;
                    list-style: none;
                    float: left;
                    margin-top: 0;
                    margin-bottom: 0;

                    li {
                        a {
                            padding: 0.5rem 0.5rem 0.5rem;

                            i {
                                display: inline-block;
                                vertical-align: middle;
                                color: $teal;
                                font-size: 24px;
                                cursor: pointer;
                            }
                        }

                        margin: 0;
                        display: inline-block;
                        float: left;

                        .btn {
                            color: $blue;
                        }
                    }
                }
            }
        }
    }
    .utility{
        display: none;
    }
    .primary-nav-list{
        display: none;
    }
}
