
@include media-breakpoint-between(md, lg) {
   
    .insight-slider{
    
        padding-top: 30px;
        padding-bottom: 30px;
        align-items: center;
      //  background-image: url(/images/insight-slider-bg.png);
        background-repeat: no-repeat;        
        .container{
            h2{
                margin-top: 5%;
                color: $blue;
                text-align: center;
                font-size: 28px;
                font-weight: $regular;
            }
        }
    }
    
    button.owl-prev{
        top:-5%;
        left: -4%;
        position: absolute;
        background: transparent;border: none;
        &:hover{
            border: none;
        }
        &:active{
            border: none;
        }
        &:focus{
            border: none;
        }
        span{
        font-size: 36px; background: transparent;border: none;
        &:hover{
            background: transparent;border: none;
        }
        &:active{
            background: transparent;border: none;
        }
        &:focus{
            border: none;
        }
        }
        
    }
    button.owl-next{
        top:-5%;
        right: -4%;
        position: absolute;
        background: transparent;border: none;
        &:hover{
            border: none;
        }
        &:active{
            border: none;
        }
        &:focus{
            border: none;
        }
        span{
            font-size: 36px; background: transparent;border: none;
            &:hover{
                background: transparent;border: none;
            }
            &:active{
                background: transparent;border: none;
            }
            &:focus{
                border: none;
            }
            }
    }
    }