//----------Mobile------------->>>>>>>>>>>>
@include media-breakpoint-between(xs, sm) { 
    
    .box-top{
       
        p{
            font-size: 18px;
            font-weight: $light;
            padding: 15px;
            padding-top: 0;
            i{
                color: $black;
                font-size: 20px;
                font-family: FontAwesome;
                font-style: normal;
        
            }
        }
        
        .left-box{
            float: left;
            padding:10px;
            h5{
                color: $blue;
                font-weight: $regular;
                font-size: 16px;
            }
            a{
                color: $teal;
                display: block;
                padding-top: 3px;
                padding-bottom: 3px;
                font-size: 16px;
                font-weight: $light;
            }
        }
        .right-box{
            padding:10px;
        }
    }
    .grey-bg{
        background-color: $grey;
    }
    
    }


    