
//--------------Tablet------------->>>>>>>>
@include media-breakpoint-between(md, lg) {
    .info{
      padding-bottom:15px!important;
                   .main-block{
            float: left;
            padding:5px;
            .content-block{
             //   border:1px solid $black;
	            min-height: 200px;
                background-color: $grey;
                text-align: center;
                padding: 0;
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
}
@media (max-width: 1000px) and (min-width: 767px){
    .info{
        padding-bottom:15px!important;
                     .main-block{
                         width: 100%;
              float: left;
              padding:5px;
              .content-block{
               //   border:1px solid $black;
                  min-height: 200px;
                  background-color: $grey;
                  text-align: center;
                  padding: 0;
                  img{
                    width: 100%;
                    height: auto;
                }
              }
          }
      }
  }
