/* -----------------------------------------------------------------------------
Import BASE
----------------------------------------------------------------------------- */
@import "variables";
@import "mixins";

//tablat
@media (min-width: 768px) and (max-width: 1024px) {
    .container{
        background-repeat: no-repeat;
    }
    .container-fluid {
        background-repeat: no-repeat;
    }
        h1 {
            font-size: 28px;
        }

        h2 {
            font-size: 24px;
        }

        h3 {
            font-size: 20px;
        }

        h4 {
            font-size: 18px;
        }

        h5 {
            font-size: 18px;
        }

        p {
            font-size: 16px;
            
            line-height: 1.2!important;
        }

        li {
            font-size: 16px;
            

        }
        a{
            color: $green;
            font-weight: $light;
            cursor: pointer;
            
            text-decoration: none;
            &:hover{
                text-decoration: none;
            }
        }
    }


//mobile
@media screen and (min-width: 320px) and (max-width: 767px) {
    .container{
        max-width: 700px ;
        background-repeat: no-repeat;
    }
    .container-fluid {
      
        background-repeat: no-repeat;
    }
        h1 {
            font-size: 28px;
        }

        h2 {
            font-size: 22px;
        }

        h3 {
            font-size: 18px;
        }

        h4 {
            font-size: 16px;
        }

        h5 {
            font-size: 16px;
        }

        p {
            font-size: 16px;
            
            line-height: 1.2!important;
        }

        li {
            font-size: 16px;
           
        }
        
        a{
            color: $green;
            font-weight: $light;
            cursor: pointer;
            
            text-decoration: none;
            &:hover{
            text-decoration: none;

            }
        }
    }


//desktop----------
@media (min-width: 1025px) {
    .container {
        width: 100%;
    }
    .container-fluid {
      
        
    }

    
        h1 {
            font-size: 36px;
        }

        h2 {
            font-size: 24px;
        }

        h3 {
            font-size: 20px;
        }

        h4 {
            font-size: 16px;
        }

        h5 {
            font-size: 18px;
        }

        p {
            font-size: 18px;
            line-height: 1.2!important;
        }

        li {
            font-size: 18px;
            
        }
        
        a{
            color: $green;
            font-weight: $light;
            cursor: pointer;
            text-decoration: none;
            &:hover{
                text-decoration: none;
    
                }
    
        }
    }
   

    .spacer{
        padding-top:20px;
        
    }
    .margin-0{
        margin: 0;

    }
    .margin-a-0{
        margin: 0 auto;
    }
   .padding-0{
       padding:0;
   }
   .padding-10{
       padding: 10px;
   }

   .todo{
       display: none;
   }



   
   .button-green{
       color: $white!important;
       background-image: $button-gradient-green!important;
       text-transform: uppercase;
   }
   .button-green:hover{
    background-image: $button-gradient-green-hover!important;
    color: $white!important;
    }

.button-green:active{
    background-image: $button-gradient-green-active!important;
    color: $white!important;
    }   
    .button-orange{
        color: $white!important;
        background: $orange1!important;
        display: block;
        text-transform: uppercase;

    }
    .button-orange:hover{
        background: $orange1!important;
        color: $white!important;
     }
 
 .button-orange:active{
    background: $orange1!important;
     color: $white!important;
     }   
    .margin-bottom-30{
        margin-bottom: 30px;
    }
 .learnmore{
     color: $green;
     text-transform: uppercase;
     &:hover{
        color: #02AC9A;
     }
     &:active{
         color: #02AC9A;
     }
 }   
 body{
     font-family: canada-type-gibson, Arial, sans-serif;
 }
th,td{
    text-align: center;
