



.table-content{
    width: 30%;
    align-items: center;
    margin: auto;
    padding: 20px;
            .table-img{
                padding:20px;
                height: auto;
                width: 148px;
                
            }
    .table{
        border-radius: 10px;
        tbody{
            tr{
                td{
                    border: none;
                    text-align: center;
                }
                th{
                    border: none;
                    text-align: center;
                    font-weight: $regular;
                }
                .left-table{
                    background-color: #003057;
                    color: white;

                    }
                    .right-table{
                            background-color: #f2f2f2;
                    }
                    .left-table-1{   background-color: #024377;                            
                        color: white;

                    }
                    .right-table-1{
                        background-color: #f8f8f8;
                    }
                            }
           
        }
    }
}