
//--------------tablet Css

@media (min-width: 768px) and (max-width: 1023px) {
    .serach-insight{


        .button-insight{
             .left-buttons{
                 float: left;
                 padding:20px;
                 padding-left: 0;
                 .button{
                    // background: $button-gradient-green;
                     color: white;
                     margin-left: 10px;
                     padding-top: 15px;
                     padding-bottom: 15px;
                     margin-bottom: 10px;
                     i{
                        font-family: FontAwesome;
                        font-style: normal;
                        margin-left: 15px;
                    }
                 }
                 .dropdown1{
                     float: left;
                    .button{
                    }
                        ul{
                            padding: 10px;
                            list-style: none;
                            width: 300px;
                            background-color: rgba(13, 108, 98, 0.9);
                            position: absolute;
                            height: auto;
                            z-index: 2;
                            margin-left: 10px;
                            color: white;
                            border-radius: 10px;
                            li{
                                height: 40px;
                                padding: 5px;

                              
                                i{
                                    font-family:FontAwesome; 
                                    float: right;
                                    color: white;
                                    font-style: normal;
                                    font-size: 15px;
                                    display: inline-block;
                                   padding-left: 4px;
                                   padding-bottom: 2px;
                                   border:  2px solid $white;
                                    height: 25px;
                                    width: 25px;
                                  
                            }
                           
                        }
                    }
                }
             }
             .right-buttons{
                 float: left;
                 padding:20px;
                 padding-left: 0;
                 text-align: right;
                 input{
                     float: left;
                     width: 150px;
                     padding:28px;
                     border-radius: 10px;    

                 }
                 .button{
                     background: $button-gradient-green;
                     color: white;
                     padding:15px;
                     margin-left: 10px;   
                     float: left;


                 }
             }



        } 


        .news-feed{
            .button-bottom{
                margin: 0 auto;
                width: fit-content;
                .button{
                    margin-left: 10px;
                    padding-top: 15px;
                    padding-bottom: 15px;
                    margin-bottom: 10px;
                }
   
            }
         .row{
         padding: 15px;
         padding-left: 0;
         padding-right: 0;
            .news-section{
                padding:15px;
                .news-block{
                        box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;
                    width: 100%;
                 padding-left: 0;
                 float: left;
                 min-height: 300px;
                 min-width: 200px;
                 border-radius: 10px;
                 img{
                    margin-bottom: -50px;
                    width: 100%;
                }
                 .title-block{
                     padding: 20px; padding-left: 0px;padding-bottom: 0px;
                     position: absolute;
                     left: 15px;
                     top: 4%;
                     
                     .title{
                         font-size: 20px;
                         background: #D8D8D8;
                         text-align: left;
                         padding-left: 15px;
                         color: $blue; 
                         padding-top: 5px;
                         padding-bottom: 5px;
                         padding-right: 10px;
                         text-transform: uppercase;
                         font-weight: $regular;

                     }
                 }
                 .content-block{
                     padding: 15px;
                     padding-top: 0; 
                     margin-top: 80px;
                     .headline{
                         font-size: 18px;
                         font-weight: $semi-bold;
                         color: $black;
                         text-decoration: none;
                        &:hover{
                            color: $black;
                            text-decoration: none;
                        }
                     }
                     .date{
                        color: $skyblue!important;

                     }
                     .learnmore{
                             color: $green;
                             position: absolute;
                             bottom: 15px;
                     }
                     .descripton{
                        font-weight: $light;
                        font-size: 14px;
                        margin-bottom: 30px;
                        
                        color: $black;
                        text-decoration: none;
                        &:hover{
                            color: $black;
                            text-decoration: none;
                        }
                    }
                 }
                }
        }
     }
}}


}