

@include media-breakpoint-up(lg) { 
    .twocolumnbanner {
        background-color: $blue;

        .row {
            margin: 0;
            text-align: center;
            width:100%;
            height: 100%;
            .content-block {
                display: block;
                width: 100%;
                padding-top: 20px;
                margin: 0 auto;
                display: flex;
                flex-flow: row wrap;
                justify-content: center;
                margin:0  auto;
                padding-left: 5px;
                .content {
                     background-color: white;
                    padding-left: 0;

                    .content-img {
                        float: left;
                        padding-right: 0;
                        margin-right: -20px;
                        padding-left: 0;

                        img {
                            float: left;
                            min-height: 100%;
                            width: 100%;
                        }
                    }

                    .content-text {
                        padding: 10px;
                        padding-left: 30px;
                        text-align: left;

                        h2 {
                            color: $blue;
                            font-size: 28px;
                            text-align: left;
                        }

                        p {
                            font-size: 20px;
                            font-weight: $light;
                        }

                        ul {
                            list-style: none;
                            padding-left: 0;

                            li {
                                font-weight: $light;
                                text-align: left;

                                i {
                                    color: $green;
                                }
                            }
                        }

                        .button {
                       //    background: $button-gradient-green;
                            padding: 15px;
                            color: $white;
                            display: block;
                            width: fit-content;
                            i{
                                color: $white;
                                padding-right: 5px;
                            }
                        }
                    }
                }
            }
        }

        .bottom-text {
            color: $white;
            padding-top: 30px;
            padding-bottom: 20px;
            font-style: italic;
            padding-left: 300px;
            padding-right: 300px;

            p {
                font-size: 14px;
            }
        }
    }
}



