//mobile
.article-body-container{
    background: $light-grey;
    background-image: linear-gradient($white,$light-grey);
    display: flow-root;
}
.body-container{
    display: flow-root;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
@include media-breakpoint-between(xs, sm) { 

    .generic { 
     
        padding-bottom: 20px;
        .container {
            padding: 0px;
            

            p {
                color: $black;
                font-weight: $light
            }

            h1 {
                color: $blue-dark;
                margin-bottom: 10px;

            }

            h2 {
                color: $blue-dark;
                margin-top: 15px;
                margin-bottom: 15px;font-weight: $regular;
            }

            h3 {
                color: $green;
                margin-bottom: 15px;font-weight: $regular;
            }

            h4 {
                line-height: 1;
                color: $black;
               
                margin-top: 10px;font-weight: $regular;
            }

            h5 {
                line-height: 1;
                color: $black;
                font-weight: $regular;
            }

            p {
                color: $dark-grey;
                line-height: 1;
                text-align: left;
                margin-top: 15px;
            }

            li {
                font-style: italic;
                font-weight: $light;

            }
            img{
                float: unset;
                display: unset;
                max-width: 100%;
            }

            p.quota {
                color: $green;
                font-size: 21px;
                border-top: 2px dashed $green;
                border-bottom: 2px dashed $green;
                padding-top: 15px;
                padding-bottom: 15px;
                line-height: 1.2;
            }

            u {
                color: $green;
            }
           
        }
        .article-img{
            padding:0!important;
            img {
                padding: 0;
                display: block;
                height: auto;
                width: 100%;
                padding-right: 0;
                padding-bottom: 10px;
             }
        }
    }
    
}

//CSS for Desktop--------->
@media (min-width: 1025px) {
    .container {
        
        margin: 0 auto;
        padding: 0px;
    }

    .generic { 
        padding-bottom: 20px;
       

        h1 {
            color: $blue-dark;
            margin-bottom: 50px;
            padding-left: 8.33%;
            padding-right: 100px;
        }
        
        

        .container {
            padding: 0px;
            
            .article-img {
                float: right;
                img {
                    width: 100%;
                    padding: 10px;
                    display: block;
                    height: auto;
                    float: right;
                    max-width: 100%;
                    align-self: center;
    
                }
            }
            img{
                float: unset;
                display: unset;
                max-width: 100%;
            }

