//tablat---------
@media (min-width: 768px) and (max-width: 1024px) {
    .generic { 
        padding-bottom: 20px;
     

        h1 {
            color: $blue-dark;
           
            padding-left: 10px;

        }

        .container {
            padding: 0px;

            .article-img {
                float: right;
                
            img {
                padding: 10px;
                display: block;
                height: auto;
                width: 100%;
                float: right;
            }
            }

            img{
                float: unset;
                display: unset;
                max-width: 100%;
            }

            p {
                color: $black;
                font-weight: $light;
            }

            h1 {
                color: $blue-dark;
                margin-bottom: 10px;



            }

            h2 {
                color: $blue-dark;
                margin-top: 15px;
                margin-bottom: 15px;
            }

            h3 {
                color: $green;
                margin-bottom: 15px;
            }

            h4 {
                line-height: 1;
                color: $black;
                font-weight: $regular;
                margin-top: 10px;
                padding: 0; 
                          
            }

            h5 {
                line-height: 1;
                color: $black;
                font-weight: $regular;
            }

            p {
                color: $dark-grey;
                line-height: 1;
                text-align: left;
                margin-top: 15px;
            }

            li {
                font-style: italic;
                font-weight: $light;

            }

            p.quota {
                color: $green;
                font-size: 21px;
                border-top: 2px dashed $green;
                border-bottom: 2px dashed $green;
                padding-top: 15px;
                padding-bottom: 15px;
                line-height: 1.2;
                font-weight: $light;
            }

            u {
                color: $green;
            }

        }
        .col-md-12{
            padding-left: 0px;
        }
    }
    
}

.article-heading {
    padding: 0px;
}



.article-content{
   table{
       text-align: center;
       align-items: center;
       tbody{
           tr{
               td{
                   border: 1px solid $black;
                   text-align: center;
                   vertical-align: middle;
                   padding: 10px;
               }
           }
       }
       thead{
        tr{
                    
            th{
                border: 1px solid $black;
                text-align: center;
                vertical-align: middle;
                padding: 10px;
            }
        }
       }
   }
   figcaption{
       padding: 20px;
   }
       
@media (min-width:1368px){

    iframe{
        width: 560px !important;
        height: 315px !important;
    }
    }
    @media (min-width:0px) and (max-width:500px){
        iframe{
            width: 100% !important;
        }
    }
    @media (min-width:501px) and (max-width:767px){
        iframe{
            width: 100% !important;
            height: 300px!important;
        }
    }
    
    @media (min-width:768px) and (max-width:1023px){
        iframe{
            width: 100% !important;
            height: 310px;
        }
    }
    
    
    @media (min-width:1024px) and (max-width:1367px){
        iframe{
            width: 500px !important;
        }
    }

}