@-webkit-keyframes cd-pulse {
  0% {
    -webkit-transform: scale(1);
    box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.8); }
  50% {
    box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.8); }
  100% {
    -webkit-transform: scale(1.6);
