@-moz-keyframes cd-pulse {
  0% {
    -moz-transform: scale(1);
    box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.8); }
  50% {
    box-shadow: inset 0 0 1px 1px rgba(255, 255, 255, 0.8); }
  100% {
    -moz-transform: scale(1.6);
    box-shadow: inset 0 0 1px 1px transparent; } }

@keyframes cd-pulse {
  0% {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
