



//----------------Desktop-------------->>>>>>>>
@include media-breakpoint-up(lg) { 
    .info{
         padding-bottom:15px!important;
                  .row{
                 margin-bottom: -10px;
                 margin-top: -10px;
                    margin: 0 auto;
                // width: fit-content;
        .main-block{
            float: left;
            padding-right: 5px!important;
            padding: 5px;
            padding-top: 20px;
            padding-bottom: 20px;
            .content-block{
               // border:1px solid $black;
                min-height: 150px;
                text-align: center;
                background-color: $grey;
                padding: 0;
                img{
                    width: 100%;
                    height: auto;
                }
    
            }
        }
    }
    }
    
.info-1{
    width: 25%;
    margin: 0 auto;
    .main-block{
        width: 100%;
    }
}
.info-2{
    width: 50%;
    margin: 0 auto;
    .main-block{
        width: 50%;
    }
}
.info-3{
    width: 75%;
    margin: 0 auto;
    .main-block{
        width: 33%;
    }
}

.info-multi{
    width: 100%;
    margin: 0 auto;
    .main-block{
        width: 25%;
    }
}

}
.blue-bg{
    background-image: linear-gradient(#00203B, #003057);

}
.main-block:focus, .main-block:hover, .main-block:active,.main-block{
    text-decoration: none;
    outline: none;


}