.accordion{
    padding-top: 15px;
    .accordion-tabs{
        h1{
            color: $blue;
            padding-top: 20px;
            padding-bottom: 10px;
        }
        p{
            font-weight: $light;
            margin-bottom: 30px;
            font-size: 22px;
        }
        div{
            font-weight: $light;
            
            font-size: 22px;
        }
        .accordion-content{
            padding: 0;
            margin-bottom: 10px;
            box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;
            .card-header{
                .accordion-btn{
                    font-size: 28px;
                    color: $blue;
                    font-weight:$regular;
                    width: 100%;
                    text-align: left;
                    text-decoration: none;
                    i{
                        float: right;
                        &:before
                        {
                            font-family: fontawesome;
                            content: "\f068";
                            font-style: normal;
                            font-size: 22px;font-weight: 400;
                        }
                    }

                    &.collapsed{
                        i{
                            &:before
                            {
                                font-family: fontawesome;
                                content: "\f067";
                                font-style: normal;
                                font-size: 22px;
                                font-weight: 400;
                                
                            }
                        }
                    }
                }
            }
            .collapse{
                background-color: $grey;
               
               
            }
          
        }
    }
}
.plusminus{
    float:right;
    font-size: 22px;font-weight: 400;
}

    
