

@media (min-width: 320px) and (max-width: 767px) {

.breadcrumb-container {
    background: $grey;

    .breadcrumb {
        background: none;
        margin-bottom: 0px;
        padding-left: 10px;

        > li {
            color: $maroon;
            text-transform: uppercase;
            padding-left: 3px;
            font-size: 11px;
            > a {
                color: $maroon;
                font-weight: $regular;
                text-transform: uppercase;
            }
        }
    }
}

}


@media (min-width: 768px) and (max-width: 1200px) {

    .breadcrumb-container {
        background: $grey;
    
        .breadcrumb {
            background: none;
            margin-bottom: 0px;
            padding-left: 10px;
    
            > li {
                color: $maroon;
                text-transform: uppercase;
                padding-left: 3px;
                font-size: 12px;
                > a {
                    color: $maroon;
                    font-weight: $regular;
                    text-transform: uppercase;
                }
            }
        }
    }
    
    }
    
    @media (min-width:1201px){
        


    .breadcrumb-container {
        background: $grey;
    
        .breadcrumb {
            background: none;
            margin-bottom: 0px;
            padding-left: 0px;
            padding-top: 5px;
            padding-bottom: 5px;
            > li {
                color: $maroon;
                text-transform: uppercase;
                padding-left: 3px;
                font-size: 16px;
                > a {
                    color: $maroon;
                    font-weight: $regular;
                    text-transform: uppercase;
                }
            }
        }
    }
    
    
    
    
    }
    

