.alternate-callout1-grey-right,.alternate-callout1-grey-left{
    display: block;
    width: 100%;
    .mobile {

        .alternatecallout-main {
            position: relative;
            display: block;
            margin-left: 0;
            margin-right: 0;

            .alternatecallout-imgdiv {
                width: 100%;
                background-image: url('/images/twocolumn-whitetheme-mobile.png');
                background-position: center;
                padding-top: 57%;
                background-size: 100% auto;
                position: relative;
                background-repeat: no-repeat;
            }
            .alternatecallout-contentdiv {
                width: 100%;
                z-index: 10;
                position: relative;
                transform: none;
                padding-bottom: 11px;
                left: 0%;

                > div {
                    width: 100%;
                    transform: none;
                    padding-left: 5%;
                    padding-right: 5%;
                    padding-bottom: 20px;

                    > h2 {
                        font-weight: $regular;
                        font-size: 22px;
                        text-align: left;
                        color: $blue;
                    }

                    > p {
                        font-size: 16px;
                        font-weight: $light;
                        color: $black;
                    }

                    > a {
                        font-size: 16px;
                        font-weight: $regular;
                        text-transform: uppercase;
                        color: $green;
                        
                    }
                    p{
                        a{
                            font-size: 16px;
                        font-weight: $regular;
                        text-transform: uppercase;
                        color: $green;  
                        }
                    }
                        ul{
                            list-style-type: disc;
                           
                            padding-left:20px;
                            overflow:hidden;
                            }
                            
                            ul li{
                            color: black;
                            a{
                                font-size: 16px;
                                font-weight: $regular;
                                color: $black;
                                text-transform: none; 
                            }
                            }
                }   
            }

            .alternatecallout-gradient {
                position: absolute;
                width: 100%;
                top: -118px;
                z-index: -1;
                height: 300px;
            }

            &.img-left {
                .alternatecallout-imgdiv {
                    float: left;
                }

                .alternatecallout-gradient {
                    right: 0;
                    left: auto;
                    margin-top: 5px;
                }

                .alternatecallout-contentdiv {
                    color: $white;
                    float: right;
                }

                &.blue {
                    .alternatecallout-gradient {
                        background-image: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,85,0.18) 5%,rgba(0,48,80,0.36) 10%,rgba(0,48,81,1) 28%,rgba(0,47,86,1) 100%);
                        /*linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(0,0,85,0.25) 6%,rgba(0,48,80,0.42) 10%,rgba(0,40,81,0.46) 11%,rgba(0,41,90,0.58) 14%,rgba(0,49,88,0.71) 17%,rgba(0,44,89,0.75) 18%,rgba(0,48,87,0.83) 20%,rgba(0,48,87,1) 24%,rgba(0,47,86,1) 100%);*/
                    }

                    background-color: #002f56;
                }

                &.grey {
                    .alternatecallout-gradient {
                        //background-image: linear-gradient(to right, red, yellow);
                        background-image: linear-gradient(to bottom, rgba(247,247,247,0) 0%,rgba(247,247,247,1) 25%,rgba(247,247,247,1) 100%);
                    }

                    background-color: #f7f7f7;
                }
            }

            &.img-right {
                .alternatecallout-imgdiv {
                    float: right;
                }
                /*.alternatecallout-gradient {
                    right: auto;
                    left: 0;
                    margin-top: 17px;
                }*/
                .alternatecallout-contentdiv {
                    > div {
                        /*padding-left: 15%;*/
                    }

                    color: $black;
                    float: left;
                }

                &.blue {
                    .alternatecallout-gradient {
                        background-image: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,85,0.18) 5%,rgba(0,48,80,0.36) 10%,rgba(0,48,81,1) 28%,rgba(0,47,86,1) 100%);
                    }

                    background-color: #002f56;
                }

                &.grey {
                    .alternatecallout-gradient {
                        background-image: linear-gradient(to bottom, rgba(247,247,247,0) 0%,rgba(247,247,247,1) 25%,rgba(247,247,247,1) 100%);
                    }

                    background-color: #f7f7f7;
                }
            }
        }
    }
}

