.demo {
    background: linear-gradient(to right, #ffcccc, #d3d3d3);
}

.post-slide2 {
    margin: 0 15px;
    box-shadow: 0 1px 2px rgba(43, 59, 93, 0.3);
    margin-bottom: 2em;

    .post-img {
        overflow: hidden;

        img {
            width: 100%;
            height: auto;
            transform: scale(1);
            transition: all 1s ease-in-out 0s;
        }
    }

    &:hover .post-img img {
        transform: scale(1.08);
    }

    .post-content {
        background: #fff;
        padding: 20px;
    }

    .post-title {
        font-size: 17px;
        font-weight: 600;
        margin-top: 0;
        text-transform: capitalize;

        a {
            display: inline-block;
            color: #808080;
            transition: all 0.3s ease 0s;

            &:hover {
                color: #3d3030;
                text-decoration: none;
            }
        }
    }

    .post-description {
        font-size: 15px;
        color: #676767;
        line-height: 24px;
        margin-bottom: 14px;
    }

    .post-bar {
        padding: 0;
        margin-bottom: 15px;
        list-style: none;

        li {
            color: #676767;
            padding: 2px 0;

            i {
                margin-right: 5px;
            }

            a {
                display: inline-block;
                font-size: 12px;
                color: #808080;
                transition: all 0.3s ease 0s;

                &:after {
                    content: ",";
                }

                &:last-child:after {
                    content: "";
                }

                &:hover {
                    color: #3d3030;
                    text-decoration: none;
                }
            }
        }
    }

    .read-more {
        display: inline-block;
        padding: 10px 15px;
        font-size: 14px;
        font-weight: bold;
        color: #fff;
        background: #e7989a;
        border-bottom-right-radius: 10px;
        text-transform: capitalize;
        transition: all 0.30s linear;

        &:hover {
            background: #333;
            text-decoration: none;
        }
    }
}