/* BOOTSTRAP ---------------------------------------------------------------- */

// Overrides
$grid-columns: 12;
$grid-gutter-width: 30px;
$grid-breakpoints: ( // Extra small screen / phone
xs: 0, // Small screen / phone
sm: 480px, // Medium screen / tablet
md: 767px, // Large screen / desktop
lg: 1024px, // Extra large screen / wide desktop
xl: 1400px // Match these to $breakpoints variables
);
$container-max-widths: ( // < @576 = 100%
sm: 420px,
md: 720px,
lg: 960px,
xl: 1380px, // max
g: 1640px
);
// Required

@import "node_modules/bootstrap/scss/bootstrap";

