/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/

@media (min-width: 320px) and (max-width: 2000px) {
    .footer-content {
        background: rgba(54,99,115,1);
        background: -moz-linear-gradient(left, rgba(54,99,115,1) 0%, rgba(54,99,115,1) 49%, rgba(40,43,71,1) 100%);
        background: -webkit-gradient(left top, right top, color-stop(0%, rgba(54,99,115,1)), color-stop(49%, rgba(54,99,115,1)), color-stop(100%, rgba(40,43,71,1)));
        background: -webkit-linear-gradient(left, rgba(54,99,115,1) 0%, rgba(54,99,115,1) 49%, rgba(40,43,71,1) 100%);
        background: -o-linear-gradient(left, rgba(54,99,115,1) 0%, rgba(54,99,115,1) 49%, rgba(40,43,71,1) 100%);
        background: -ms-linear-gradient(left, rgba(54,99,115,1) 0%, rgba(54,99,115,1) 49%, rgba(40,43,71,1) 100%);
        background: linear-gradient(to right, rgba(54,99,115,1) 0%, rgba(54,99,115,1) 49%, rgba(40,43,71,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#366373', endColorstr='#282b47', GradientType=1 );

        padding: 0;
        margin: 0;
    }

    .footer-menu-item {
        text-transform: uppercase;
    }

    .alt-text {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 1px;
        height: 1px;
        overflow: hidden;
    }

    .expand-footer-wrap {
        padding: 0;
        margin: 0;
        color: $white;
        text-decoration: none;
        padding: 10px;
        border-bottom: 1px solid black;
        line-height: 45px;
        position: relative;
        font-size: 22px;

        &:after {
            clear: both;
            content: '';
            display: block;
        }

        .footer-menu-item {
            display: block;

            a {
                font-size: 20px;
            }
        }

        ul {
            display: none;
            padding: 0;

            li {
                position: relative;
            }

            a {
                font-size: 16px;
                display: block;
                font-weight: 400;
                padding-top: 13px;
                padding-bottom: 12px;
                line-height: normal;
            }
        }

        ul.depth2 {
            li {
                list-style: none;
            }

            a {
                font-size: 16px;
            }

            .expand-button {
                top: -6px;
                width: 30px;
            }
        }

        ul.depth3 {
            padding-left: 22px;
        }

        span {
            word-wrap: break-word;
            font-weight: 400;

            a {
                color: #fff;
                font-weight: 600;
            }
        }

        span:first-child {
            display: block;

            a {
                padding-left: 20px;
                padding-right: 20px;
                width: 84%;
            }
        }

        .expand-button {
            position: absolute;
            right: 0;
            top: 0;
            height: 46px;
            width: 51px;
            text-align: center;
            padding: 0;

            a {
                color: #333;
                text-decoration: none;
                padding-top: 0;
                padding-bottom: 0;
            }

            .hide {
                display: none;
            }

            .fa.fa-plus:before {
                color: #F7BE00;
                font-size: 27px;
                font-weight: normal;
                display: block;
                margin-top: 19px;
            }

            .fa.fa-minus:before {
                color: #F7BE00;
                font-size: 27px;
                font-weight: normal;
                display: block;
                margin-top: 19px;
            }
        }
    }
}
