//Controls ------------
.owl-theme .owl-controls .owl-page span{
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	border-radius: 0;
  width: 100px;
  height: 5px;
  margin-left: 2px;
  margin-right: 2px;
  background: #ccc;
  border:none!important;
}
.owl-theme .owl-nav .owl-prev:hover{
    background: transparent;
    color: $skyblue;
    border: none!important;
}
.owl-theme .owl-nav .owl-next:hover{
    background: transparent;
    color: $skyblue;
    border: none!important;
}

button.owl-prev{
    top:-9%;
    left: -4%;
    height: 100%;
    width: 5%;
    position: absolute;
    background: transparent;
    color: $skyblue;
    border: none !important;
    span{
    font-size: 36px; background: transparent;border: none!important;
    &:hover{
        background: transparent;border: none!important;
    }
    &:active{
        background: transparent;border: none!important;
    }
    }
    
}
button.owl-next{
    top:-9%;
    right: -4%;
    height: 100%;
    width: 5%;
    position: absolute;
    background: transparent;
    color: $skyblue;
    border: none!important;
    span{
        font-size: 36px; background: transparent;border: none!important;
        &:hover{
            background: transparent;border: none!important;
        }
        &:active{
            background: transparent;border: none!important;
        }
        }
}


.testimonial .title-img{
    font-size: 15px;
    text-transform: uppercase;
    background: #D8D8D8;
    text-align: left;padding-left: 20px;
    color: $blue;
    position: absolute;
    top: 30px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    font-weight: $regular;
  }

.bg-top-right{
    background-position: top right;
    background-repeat: no-repeat;
}
.bg-top-left{
    background-position: top left;
    background-repeat: no-repeat;
}
.bg-top-center{
    background-position: top center;
    background-repeat: no-repeat;
}
.bg-bottom-right{
    background-position: bottom right;
    background-repeat: no-repeat;
}
.bg-bottom-left{
    background-position: bottom left;
    background-repeat: no-repeat;
}
.owl-carousel .owl-stage{
  margin: 0 auto;
}
.owl-next:focus, .owl-prev:focus {outline: none;}
.owl-next:active, .owl-prev:active {outline: none;}
.owl-next, .owl-prev {outline: none;}
.owl-next:hover, .owl-prev:hover {outline: none;}