
.imagemodelwindow{
  padding-top: 15px;
    .row{
      .col-md-12{
        padding-left: 0;
        padding-right: 0;
      }
    .content-div{
        padding:10px;
        float: left;
        padding-left: 15px;
        padding-right: 15px;
        .content-body{
            padding:20px;
            box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;            
            border-radius: 5px;
            background-color: $white;
            h2{
                font-size: 28px;
                color: $blue;
                text-align: left;
            }
            .text-expand1,.text-expand2{
              font-weight: $light;
            //  height: auto;
         //     overflow: hidden;
            }
            button{
              color:$teal;
              display: block;
              text-decoration: none;
              font-size: 20px;
              font-weight: 500; 
              i{
                color: $teal;
                margin-right: 15px;
              }
              button:hover{
                text-decoration: none;
                border: none;
                outline: none;
              }
              
              button:active{
                text-decoration: none;
                border: none;
                outline: none;
              }
            }
            a{
                color: $green;
            }
            ul{
              overflow: hidden;
              list-style: inherit;
              padding-left: 20px;
              li{
                font-weight: $light;
              }
            }
            .carousel{
                ol{
                    li{
                        background-color: $blue;
                        width: 10px;
                        height: 10px;
                        border-radius: 100%;
                    }
                }


            .carousel-inner{
              .carousel-item{
                height: 300px;
                img{
                  height:270px;
                }
              }
            }
            }
        .card{
            border: none;
            .collaspe{
              .card-body{
                
              }
            }
            
            .card-header{
                background-color: $white;
                border-bottom: none;
                padding:0;
                button{
                    color: $green;
                    text-decoration: none;
                    i{
                        
                        &:before
                        {
                            font-family: fontawesome;
                            content: "\f068";
                            font-style: normal;
                            margin-right: 5px;
                        }
                    }

                    &.collapsed{
                      
                        i{
                            &:before
                            {
                                font-family: fontawesome;
                                content: "\f067";
                                font-style: normal;
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        }
        }
    }
  }

}
.expand-one-id, .expand-two-id{
  display: none;
}

//Model window css----------->>>>>>>>>>>>>>
.myImg {
    cursor: pointer;
    transition: 0.3s;
    width: 100%!important;
    padding: 10px;  
  }
  #myImg2 {
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
  }
  #myImg3 {
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  #myImg:hover {opacity: 0.7;}
  
  /* The Modal (background) */
  .modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 30; /* Sit on top */
    padding-top: 100px!important; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
  }
  
  /* Modal Content (image) */
  .modal-content {
    display: block;
    width: auto;
    position: fixed;
    top: 50%;
    left: 50%;
    /* bring your own prefixes */
    transform: translate(-50%, -50%);
  }
  
  /* Caption of Modal Image */
  #caption {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
    text-align: center;
    color: #ccc;
    padding: 10px 0;
    height: 150px;
  }
  
  /* Add Animation */

  /* The Close Button */
  .close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
  }
  
  .close:hover,
  .close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }
  
  /* 100% Image Width on Smaller Screens */
  @media only screen and (max-width: 700px){
    .modal-content {
      width: auto;
    }
  }
 
