 }
 .slider-control-blue{
  color: $blue;
  font-size: 32px;
  
 }
 .imagemodelwindow .content-div .content-body .carousel-control-prev {
  color: #008065;
  position: absolute;
  left: -2%;
  i{
    left: -2%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}

.imagemodelwindow .content-div .content-body .carousel-control-next {
  color: #008065;
  position: absolute;
  right: -2%;
  
  i{
    right: -2%;
    position: absolute;
    top: 50%;
  transform: translateY(-50%);
  }
}

.carousel-indicators{
  bottom: -4%;
}






.text-expand1 {
  position: relative;
  max-height: 100px;
  height: 100px;
  overflow: hidden;
  
  &.-expanded {
    max-height: 100%;
    height: 100%;
  }
}

.text-expand1:not(.-expanded)::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
}


.text-expand2 {
  position: relative;
  max-height: 100px;
  height: 100px;
  overflow: hidden;
  
  &.-expanded {
    max-height: 100%;
    height: 100%;
  }
}

.text-expand2:not(.-expanded)::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
}



@media (max-width: 767px){
  .carousel-indicators{
