

//-----------Mobile Css
@include media-breakpoint-between(xs, sm) { 
     
    .product-tile{
        padding-top: 20px;
        padding-bottom: 15px;
        h2{
            color: $blue;
            text-align: center;
            padding-top: 20px;
            font-size: 28px;
            font-weight: $regular;
        }
        .row{
            .product-outer{
                padding: 15px;
                float: left;
                width: 100%;
                a{
                    cursor: pointer;
                   }
                .product-tile-inner{
                        position: relative;
                        border-radius: 10px;
                img{
                        display: block;
                }
                .top-color{
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        background-color: #673277;
                        height: 100%;
                        width: 100%;
                        left: 0;
                        right: 0;
                        opacity: 0.5;transition: .5s ease;border-radius: 5px;
                      
    
                        
                        .text {
                            width: auto;
                            color: white;
                            font-size: 36px;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            -webkit-transform: translate(-50%, -50%);
                            -ms-transform: translate(-50%, -50%);
                            transform: translate(-50%, -50%);
                            text-align: center;
                            border-top: 2px solid $white;
                            border-bottom: 2px solid $white;
                            
                            z-index: 100;
                            font-style: bold;       transition: transform 10s;
                            ::before{
                               
                            }
                        }
                }
                }
            }
        }
    
    }
    
    
    
    
    .blue-color-effect{
        background-color: #002D74!important;
    
    }
    .green-color-effect{
        background-color: #0083AD!important;
    }
    
    }
    