#menu {
    position: sticky;
    left: 0;
    top: 430px;
    width: 8em;
    margin-top: -4.6em;
    border-radius: 10px;
    z-index: 26;
    width: 60px;
  }
  .menu-bar{
      list-style: none;
      padding-left: 0;
      background-color: $blue;
      color: white !important;
      height: 60px;
      li{
          button{
              color: $white;
          }
      }
     
     .menu-text{
         color: white;
         font-weight: 600;
         padding-left: 7px;
         margin-top: 0;
     }
  }
  .bars{
      color: white;
      padding-bottom: 0;
      font-style: normal;
      padding: 14px;
      &:hover{
        color: $white;
        outline: none;
      }
      &:active{
        outline: none;
      }

  }


  .sticky-menu-list{
      position: fixed;
      top: 225px;
      z-index: 27;
      background: linear-gradient(270deg, rgba(0,77,131,1) 100%, rgba(19,135,213,1) 100%, rgba(216,216,216,1) 100%);
      width: 350px;
      overflow-x: hidden;
      height: 70%; 
      transform: translateX(-100%);
      -moz-transition: -moz-transform .25s;
    -o-transition: -o-transform .25s;
    -webkit-transition: -webkit-transform .25s;
    transition: transform .25s;
    //   a{
    //       float: right;
    //   }
      p{
          color: $yellow;
          font-size: 24px;
          padding: 15px;
          border-bottom: 0.5px solid #0067BA;
            padding-left: 0;
            margin-left: 15px;
            margin-right: 20px;
      }
      ul{
          list-style: none;
          padding-left: 15px;
            margin-left: 0;
            padding-right: 15px;
          .menu-heading{
              color: $yellow;
              font-size: 22px;
              padding-bottom: 10px;
              display: block;
              font-weight: 400;

          }
          li{
              
              a{
                color: white;
                padding-top: 0;
                font-size: 20px;
                display: block;
                  i{
                      color: white;
                      height: 50px;
                      width: 50px;
                      font-family: fontawesome;
                      font-style: normal;
                      font-size: 30px;
                      margin-top: 10px;
                      margin-bottom: 10px;
                      float: right;
                      padding: 10px;
                  }
              }
          }
      }
  
  }

  .show-menu{
    transform: translateX(0) translateZ(0);
    -moz-transition: -moz-transform .25s;
    -o-transition: -o-transform .25s;
    -webkit-transition: -webkit-transform .25s;
    transition: transform .25s;
}
@media screen and (max-width: 1025px) {
#menu{
    display: none;
}  
.sticky-menu-list{
    display: none;
}
}
#sticky-content::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
    background: linear-gradient(270deg, rgba(0,77,131,1) 100%, rgba(19,135,213,1) 100%, rgba(216,216,216,1) 100%);
    
}

#sticky-content::-webkit-scrollbar
{
	width: 4px;
    background: linear-gradient(270deg, rgba(0,77,131,1) 100%, rgba(19,135,213,1) 100%, rgba(216,216,216,1) 100%);

}

#sticky-content::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: $yellow;
}
