
.generic-content-common{

    padding-bottom: 20px;
    .generic-content{
        padding-bottom: 10px;
        h1{
            color: $blue;
            padding-top: 20px;
            padding-bottom: 10px;
        }
        p{
            font-weight: 300;
            margin-bottom: 20px;
            font-size: 20px;
        }
        span{
            font-weight: 300;
            margin-bottom: 20px;
            font-size: 20px;
        }
        div{
            font-weight: 300;
            font-size: 20px;
        }
        img{
            float: unset;
            display: unset;
            max-width: 100%;
        }
        font-weight: 300;
        font-size: 20px;
        table{
            text-align: center;
            align-items: center;
            tbody{
                tr{
                    td{
                        border: 1px solid $black;
                        text-align: center;
                        vertical-align: middle;
                        padding: 10px;
                    }
                }
            }
            thead{
             tr{
                         
                 th{
                     border: 1px solid $black;
                     text-align: center;
                     vertical-align: middle;
                     padding: 10px;
                 }
             }
            }
        }
       
@media (min-width:1368px){

    iframe{
        width: 560px !important;
        height: 315px !important;
    }
    }
    @media (min-width:0px) and (max-width:500px){
        iframe{
            width: 100% !important;
        }
    }
    @media (min-width:501px) and (max-width:767px){
        iframe{
            width: 100% !important;
            height: 300px!important;
        }
    }
    
    @media (min-width:768px) and (max-width:1023px){
        iframe{
            width: 100% !important;
            height: 310px;
        }
    }
    
    
    @media (min-width:1024px) and (max-width:1367px){
        iframe{
            width: 500px !important;
        }
    }
    }
    
}

