.ma5-menu-panel {

    width: 100%;
    height: 90%;
    position: absolute;
    overflow: hidden;
    overflow-y: visible;
  //  top: 68px;
    right: 0 !important;
    margin: 0;
    padding: 0;
    
    list-style-type: none;
    z-index: 100;
    background-color: $blue;
    display: none;

}

.ma5-menu-panel:after {
    content: "";
    display: block;
    position: absolute;
    width: 1px;
    height: 90%;
    right: 0;
    top: 0;
    background-color: $blue;
}

.ma5-menu-panel li {
    position: relative;
   // padding: 0 16px;
    min-height: 20px;
    background-color: $blue;
    color: $white;
}

.ma5-menu-panel a {
    line-height: 1.5;
    padding: 10px 0;
    font-size: 16px;
    display: block;
    position: relative;
    color: white;
    margin-left: 15px;
    font-weight: 500;

    .nav-arrow-big {
        &:before {
            color: $yellow;
            content: "\f054";
            display: block;
            font-size: 14px;
            font-family: 'FontAwesome';
            left: inherit;
            position: absolute;
            transform: translateY(-50%);
            right: 20px;
            top: 50%;
        }
    }

    .nav-arrow-small {
        &:before {
          //  color: $navBorder;
          content: "\f054";
          display: block;
            font-size: 14px;
            font-family: 'FontAwesome';
            left: inherit;
            position: absolute;
            transform: translateY(-50%);
            right: 20px;
            top: 50%;
            color: $yellow;
        }
    }
}

.ma5-menu-panel .ma5-btn-enter {
    font-size: 16px;
    cursor: pointer;
    display: inline-block;
    position: relative;
    padding: 16px 15px 14px 15px;
    width: 20%;
    height: 52px;
    overflow: hidden;
    left: 0;
    top: 0;
    text-align: center;
    color: white;
    background: transparent;
    border: 0;
    border-left: 1px solid #cfcfcf;
    float: right;
}

.ma5-menu-panel .ma5-btn-leave {
    font-size: 14px;
    margin-left: 15px;
    text-transform: uppercase;
    cursor: pointer;
    display: block;
    width: 100%;
    overflow: hidden;
    left: 0;
    top: 0;
    text-align: left;
    border: 0;
    background: transparent;
    max-height: 47px;
}

.ma5-menu-panel .ma5-btn-leave .menu-arrow {
    font-size: 16px;
    padding-right: 5px;
}

.ma5-menu-panel .ma5-btn-enter .glyphicon, .ma5-menu-panel .ma5-btn-leave .glyphicon {
    top: 2px;
}

.ma5-menu-panel .ma5-btn-enter {
    z-index: 0;
    left: auto;
    right: 0;
}

.ma5-menu-panel .ma5-leave-bar {
    font-size: 16px;
    cursor: pointer;
    display: block;
    position: relative;
    padding: 0px;
    margin-right: -50px;
    text-align: center;
    color: #668aad;
    text-transform: uppercase;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.ma5-leave-bar{
    display: inline-flex!important;
    a{
        color: $white!important;
    }
}

.ma5-menu-panel .ma5-leave-bar:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    bottom: 1px;
    background-color:$blue;
}

.ma5-menu-panel:first-of-type {
    z-index: 160;
}

.ma5-menu-panel.ma5-active-ul {
    z-index: 170;
    display: block;
}

.ma5-menu-panel.ma5-parent-leave, .ma5-menu-panel.ma5-parent-enter {
    z-index: 180;
}

.ma5-menu-panel.ma5-active-leave, .ma5-menu-panel.ma5-active-enter {
    z-index: 190;
    li{
        .row{
            .col-2{
                margin-left: 5%;
            }
        }
    }
}

.ma5-menu-panel.ma5-active-enter {
    -webkit-animation-timing-function: ease-out;
    -webkit-animation-duration: 0.4s;
    -webkit-animation-delay: 0;
    -webkit-animation-fill-mode: both;
    -webkit-animation-name: ma5-panelIn;
    animation-timing-function: ease-out;
    animation-duration: 0.4s;
    animation-delay: 0;
    animation-fill-mode: both;
    animation-name: ma5-panelIn;
    height: 90%;
    overflow: scroll;
    display: block;
}

.ma5-menu-panel.ma5-active-leave {
    -webkit-animation-timing-function: ease-out;
    -webkit-animation-duration: 0.4s;
    -webkit-animation-delay: 0;
    -webkit-animation-fill-mode: both;
    -webkit-animation-name: ma5-panelOut;
    animation-timing-function: ease-out;
    animation-duration: 0.4s;
    animation-delay: 0;
    animation-fill-mode: both;
    animation-name: ma5-panelOut;
}

