
@import "mobile/generic-content-mobile";
.h1-content{
    padding-top: 1.5%;
    padding-bottom: 2.5%;
    margin-left: 11%;
    font-size: 36px;
}
#font-class{
    font-size: 24px;
}
.article{
    padding-bottom: 20px;

    ul{
        list-style: disc;
        font-weight: $light;
        padding-inline-start: 40px;
    }
    ol{
        list-style-type: decimal;
        font-weight: $light;
        padding-inline-start: 40px;
    }
    figcaption{
        padding: 20px;
    }
     
@media (min-width:1368px){

    iframe{
        width: 560px !important;
        height: 315px !important;
    }
    }
    @media (min-width:0px) and (max-width:500px){
        iframe{
            width: 100% !important;
            height: 215px;
        }
    }
    @media (min-width:501px) and (max-width:767px){
        iframe{
            width: 100% !important;
            height: 285px!important;
        }
    }
    
    @media (min-width:768px) and (max-width:1023px){
        iframe{
            width: 100% !important;
            height: 310px;
        }
    }
    
    
    @media (min-width:1024px) and (max-width:1367px){
        iframe{
            width: 500px !important;
            height: 330px;
        }
    }
 
}
#st-1 .st-btn > img{
    padding: 0;
}




     
@media (min-width:1368px){
    .video-player{
        width: 50%;
    }
  
    }
    @media (min-width:0px) and (max-width:500px){
        .video-player{
            width: 100%;
        }
     
    }
    @media (min-width:501px) and (max-width:767px){
        .video-player{
            width: 70%;
        }
    }
    
    @media (min-width:768px) and (max-width:1023px){
        .video-player{
            width: 70%;
        }
    }
    
    
    @media (min-width:1024px) and (max-width:1367px){
        .video-player{
            width: 50%;
        }
    }
 