.alternate-callout1-grey-left,.alternate-callout1-blue-left{
.video-alternet{
    
    
    h2{
        color: $blue;
        
    }
    p{
        line-height: 1.2;
    }
    a{
    color: $teal;
    }
}
@media (min-width: 1501px) {
    .video-section{
        min-height: 315px;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        right: 0%;
        z-index: 25;
        h2{
            top: 50%;
            left: 50%;
            position: absolute;
            transform: translate(-50%,-50%);
        }
        
    }
    }

@media screen and (min-width: 1351px) and (max-width: 1500px) {
.video-section{
  
    
