
@include media-breakpoint-up(lg) { 
.featurekit {
        padding: 20px;
        h1 {
            color: $blue;
            text-align: center;
        }

        .featurekit-content {
            display: flex;
            float: left;

            .content-kit {
                background: $white;
                border-radius: 5px;
                width: 100%;
                position: relative;
                img.img-fluid {
                    width: 100%;
                }

                div {
                    padding: 15px;

                    h2 {
                        font-size: 28px;
                        color: $blue;
                        text-decoration: none;
                    }

                    p {
                        font-size: 18px;
                        font-weight: $light;
                        margin-bottom: 10%;
                        color: $black;
                        text-decoration: none;
                        &:hover{
                            text-decoration: none;
                            color: $black;
                        }
                        
                        &:active{
                            text-decoration: none;
                            color: $black;
                        }
                    }

                    .learnmore {
                        a {
                            color: $green;
                            position: absolute;
                            left: 50%;
                            bottom: 15px;
                            transform: translateX(-50%);
                            text-decoration: none;
                        }
                        span{
                            color: $green;
                            position: absolute;
                            left: 50%;
                            bottom: 15px;
                            transform: translateX(-50%);
                            text-decoration: none;
                        }

                        margin-top: 30px;
                    }
                }
            }
        }

        .button-center {
            .button {
                width: 48%;
                margin: 5px;
                height: 50px;
               // background: $button-gradient-green;
                margin-top: 30px;
                margin-bottom: 30px;
                color: $white;
            }

            .button:hover {
        //        background: $button-gradient-green-hover;
            }

            .button:active {
        //        background: $button-gradient-green-active;
            }
        }
    }
   
}
@media only screen and (min-width: 1023px){
.kit-1{
    width: 33%;
    margin: 0 auto;
    .featurekit-content {
        width: 100%!important;
        padding-right: 0;

    }
}
.kit-2{
    width: 66%;
    margin: 0 auto;
    .featurekit-content {
        width: 48%!important;
        padding-right: 0;
        margin: 1%;
    }
}
.kit-multi{
    width: 100%;
    margin: 0 auto;
    .featurekit-content {
        width: 32%!important;
        margin: 0 auto;
        &:first-child{
            margin-left: 0%!important;
        }
        &:last-child{
            margin-right: 0%!important;
        }
    }
}
}
