
.box-top{
   
    p{
        font-size: 18px;
        font-weight: $light;
        padding: 15px;
        padding-top: 0;
        i{
            color: $black;
            font-size: 20px;
            font-family: FontAwesome;
            font-style: normal;
    
        }
    }
    
    .left-box{
        float: left;
        padding:10px;
        h5{
            color: $blue;
            font-weight: 400;
            font-size: 18px;
        }
        a{
            color: $teal;
            display: block;
            padding-top: 3px;
            padding-bottom: 3px;
            font-size: 18px;
            font-weight: $light;
        }
    }
    .right-box{
        padding:10px;
    }
}
.grey-bg{
    background-color: $grey;
}
