
.breadcrumb-container {
    background: $grey;

    .breadcrumb {
        background: none;
        margin-bottom: 0px;

        > li {
            color: $maroon;
            text-transform: uppercase;
            padding-left: 3px;
            font-size: 11px;
            > a {
                color: $maroon;
                font-weight: $regular;
                text-transform: uppercase;
            }
            &:first-child{
                padding-left: 0;
            }
        }
    }
}


.breadcrumb-item + .breadcrumb-item::before{
    padding-right: 2px!important;
}
@import "mobile/breadcrumb-mobile";
