
@include media-breakpoint-between(xs, sm) { 
    .cd-product-wrapper {
        display: inline-block;
        position: relative;
        margin: 0 auto;
        width: auto;
        img {
            max-width: 100%;
            height: auto;
        }
    
        ol {
            list-style: none;
            padding: 0;
        }
    }
    
    ul {
        list-style: none;
        padding: 0;
    }
    
    
    
    .cd-img-replace {
        display: inline-block;
        overflow: hidden;
        text-indent: 100%;
        white-space: nowrap;
    }
    
    
    .pods-content-inner {
        padding: 60px 30px;
    }
    
    .pods-slider-wrap {
        position: relative;
    }
    
    .pods-slide-controls {
        position: absolute;
        width: 100%;
        top: 30px;
        left: 0;
        text-align: center;
        z-index: 5;
    
        li {
            display: inline-block;
            margin: 0 5px;
            width: 12px;
            height: 12px;
            border: 1px solid #000;
            background: $sunflower;
            border-radius: 100%;
    
            &:hover, &.active {
                background: #000;
            }
        }
    }
    
    .pods-slider > div {
        display: none;
    
        &.active {
            display: block;
        }
    }
    
    .pods-slide-caption {
        margin-top: 60px;
    
        p {
            margin: 30px 0 0;
            position: relative;
        }
    
        span {
         //   font-family: 'ReplicaMonoPro', sans-serif;
        }
    }
    
    .cd-more-info p {
        span {
           // font-family: 'ReplicaMonoPro', sans-serif;
            
        }
    
        font-size: 1.0rem;
        line-height: 1.1;
        margin-bottom: 10px;
    }
    
    .home-pods-2 .caption-front p:first-child:before {
        content: "";
        position: absolute;
        top: 50%;
        right: 100%;
        margin-right: 20px;
        width: 300px;
        border-top: 2px solid $yellow;
    }
    
    .cd-img-replace {
        display: inline-block;
        overflow: hidden;
        text-indent: 100%;
        white-space: nowrap;
    }
    
    .cd-product-wrapper {
        /* display:inline-block; */
        /* position:relative; */
        /* margin:0 auto; */
        /* width:100%; */
        display:block;
        > img {
            display: block;
            width:100%;
        }
    }
    
    .cd-single-point {
        position: absolute;
        border-radius: 50%;
    
        > a {
            position: relative;
            z-index: 2;
            display: block;
            width: 0.7em;
            height: 0.7em;
            border-radius: inherit;
            background: $orange;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.3);
            -webkit-transition: background-color .2s;
            -moz-transition: background-color .2s;
            transition: background-color 0.2s;
            
    
            &::after, &:before {
                content: '';
                position: absolute;
                left: 50%;
                top: 50%;
                bottom: auto;
                right: auto;
                -webkit-transform: translateX(-50%) translateY(-50%);
                -moz-transform: translateX(-50%) translateY(-50%);
                -ms-transform: translateX(-50%) translateY(-50%);
                -o-transform: translateX(-50%) translateY(-50%);
                transform: translateX(-50%) translateY(-50%);
                
            }
    
            &::after {
               content: "\f002";
               font-family: fontawesome;
               left: 0px;
               color: $white;
               font-size: 4px;
            }
    
            &::before {
                content: "\f002";
               font-family: fontawesome;
               left: 0px;
               color: $white;font-size: 4px;
            }
        }
    
        &::after {
            content: '';
            position: absolute;
            z-index: 1;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            border-radius: inherit;
            background-color: transparent;
            -webkit-animation: cd-pulse 2s infinite;
            -moz-animation: cd-pulse 2s infinite;
            animation: cd-pulse 2s infinite;
        }
    
        &.point-1-1 {
            top: 35%;
            left: 39%;
        }
    
        &.point-1-2 {
            top: 45%;
            left: 39%;
        }ss
    
        &.point-2-1 {
            top: 41%;
            left: 34%;
        }
    
        &.point-2-2 {
            top: 41%;
            left: 44%;
        }
    
        &.point-3-1 {
            bottom: 50%;
            right: 34%;
        }
    
        &.point-3-2 {
            bottom: 29%;
            right: 54%;
        }
    
        &.is-open {
            > a {
                background-color: $orange;
    
                &::after, &::before {
                  
                }
            }
    
            &::after {
                display: none;
            }
    
            .cd-more-info {
                display: block;
                opacity: 1;
                -webkit-transform: scale(1);
                -moz-transform: scale(1);
                -ms-transform: scale(1);
                -o-transform: scale(1);
                transform: scale(1);
                -webkit-transition: opacity .3s 0s, visibility 0s 0s, -webkit-transform .3s 0s, top .3s 0s, bottom .3s 0s, left .3s 0s, right .3s 0s;
                -moz-transition: opacity .3s 0s, visibility 0s 0s, -moz-transform .3s 0s, top .3s 0s, bottom .3s 0s, left .3s 0s, right .3s 0s;
                transition: opacity 0.3s 0s, visibility 0s 0s, transform 0.3s 0s, top 0.3s 0s, bottom 0.3s 0s, left 0.3s 0s, right 0.3s 0s;
            }
        }
    
        &.visited > a {
            background-color: $orange;
        }
    
        .cd-more-info {
            display: none;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 3;
            width: 100%;
            height: 100%;
            overflow-y: auto;
            -webkit-overflow-scrolling: touch;
            text-align: left;
            line-height: 1.5;
            background-color: rgba(255, 255, 255, 0.95);
            padding: 2em 1em 1em;
            opacity: 0;
            -webkit-transform: scale(0.8);
            -moz-transform: scale(0.8);
            -ms-transform: scale(0.8);
            -o-transform: scale(0.8);
            transform: scale(0.8);
            -webkit-transition: opacity .3s 0s, visibility 0s .3s, -webkit-transform .3s 0s, top .3s 0s, bottom .3s 0s, left .3s 0s, right .3s 0s;
            -moz-transition: opacity .3s 0s, visibility 0s .3s, -moz-transform .3s 0s, top .3s 0s, bottom .3s 0s, left .3s 0s, right .3s 0s;
            transition: opacity 0.3s 0s, visibility 0s 0.3s, transform 0.3s 0s, top 0.3s 0s, bottom 0.3s 0s, left 0.3s 0s, right 0.3s 0s;
    
            &::before {
                content: '';
                position: absolute;
                height: 0;
                width: 0;
                display: none;
                border: 8px solid transparent;
                display: block;
            }
    
            position: absolute;
            width: auto;
            height: auto;
            padding: 20px;
            padding-top:30px;
            overflow-y: visible;
            line-height: 1.4;
            border-radius: .25em;
            box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
            z-index: 10;
    
            &.cd-top, &.cd-bottom, &.cd-top::before, &.cd-bottom::before {
                left: 50%;
                right: auto;
             
            }
    
            &.cd-top {
                top: -266%;
                left: 136%;               
                background:  rgba(232,114,0,0.7);
                color: $white;
    
                &::before {
                    border-top-color:rgba(232,114,0,0.7);
                    left: -15px;
                    top: 42px;
                    transform: rotate(90deg);
                }
               
                a{
                    color: $white;
                    &:hover{
                        color: $blue;
                }
                }
            }
        }
    }
    
    /*.cd-single-point.visited::after{display:none}*/
    
    @-webkit-keyframes cd-pulse {
        0% {
            -webkit-transform: scale(1);
            box-shadow: inset 0 0 1px 1px $color_medium_white;
        }
    
        50% {
            box-shadow: inset 0 0 1px 1px $color_medium_white;
        }
    
        100% {
            -webkit-transform: scale(1.6);
            box-shadow: inset 0 0 1px 1px transparent;
        }
    }
    
    
    @-moz-keyframes cd-pulse {
        0% {
            -moz-transform: scale(1);
            box-shadow: inset 0 0 1px 1px $color_medium_white;
        }
    
        50% {
            box-shadow: inset 0 0 1px 1px $color_medium_white;
        }
    
        100% {
            -moz-transform: scale(1.6);
            box-shadow: inset 0 0 1px 1px transparent;
        }
    }
    
    
    @keyframes cd-pulse {
        0% {
            -webkit-transform: scale(1);
            -moz-transform: scale(1);
            -ms-transform: scale(1);
            -o-transform: scale(1);
            transform: scale(1);
            box-shadow: inset 0 0 1px 1px $color_medium_white;
        }
    
        50% {
            box-shadow: inset 0 0 1px 1px $color_medium_white;
        }
    
        100% {
            -webkit-transform: scale(1.6);
            -moz-transform: scale(1.6);
            -ms-transform: scale(1.6);
            -o-transform: scale(1.6);
            transform: scale(1.6);
            box-shadow: inset 0 0 1px 1px transparent;
        }
    }
    
    
    .cd-close-info {
        position: fixed;
        top: 0;
        right: 0;
        height: 44px;
        width: 44px;
    
        &::after, &:before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            bottom: auto;
            right: auto;
            -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
            -moz-transform: translateX(-50%) translateY(-50%) rotate(45deg);
            -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
            -o-transform: translateX(-50%) translateY(-50%) rotate(45deg);
            transform: translateX(-50%) translateY(-50%) rotate(45deg);
            background-color: $white;
            -webkit-transition-property: -webkit-transform;
            -moz-transition-property: -moz-transform;
            transition-property: transform;
            -webkit-transition-duration: .2s;
            -moz-transition-duration: .2s;
            transition-duration: 0.2s;
        }
    
        &::after {
            height: 2px;
            width: 16px;
        }
    
        &::before {
            height: 16px;
            width: 2px;
        }
    }
    

}







