
@include media-breakpoint-up(lg) { 
    .leadershipgrid{
    background-image: linear-gradient(#ffffff, #eaeaea);margin-bottom: 20px;
        
    h1{
        color: $blue;
        padding-left: 0;
        padding-top: 15px;
        font-size: 22px;
    }
    h4{
        color: $blue;
        font-style: italic;
        font-size: 18px;
       
       
    
    }
    .content{
        display: block;
        padding-bottom: 30px;
        .text-p{
            width:auto; 
            display: block;padding-left: 15px;
        p{
            font-weight: $light;
        }
    }
    img{
        float: right;
        margin-right:15px;
        margin-bottom:15px;
        margin-left: 100px;
    }
    }
    
}
.gridview{
    padding-bottom: 20px;

   .title-leader{
       font-size: 28px;
       color: $blue;
       padding: 10px;
   }
   .row{
    .grid-content{
        float: left;
        padding: 0px;
       // position: relative;
        h2{
            font-size: 24px;
            padding: 5px;
            padding-bottom: 0px;
        }

    .grid-img{
        position: relative;
        margin: 5px;
            .img-fluid{
                width: 100%;
                height: auto;
            }
    }
.text-block {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: #003057;
    color: white;
    min-height: 100px;
    background: rgba(0, 48, 87, 0.5);
    text-align: center;
  }
}
}
} 
.leader-tablet{
    h1{
        color: $blue;
        font-size: 28px;
    }
}
}

