//--------------MObile-------------------


@include media-breakpoint-between(xs, sm) { 
    .featurekit {
        padding: 20px;
        h1 {
            color: $blue;
            text-align: center;
        }
        .featurekit-content {
            display: flex;
            float: left;
            padding-left: 0;
            padding-right: 0;   
            width: 100%;
            padding-bottom: 10px;   
            .content-kit {
                width: 100%;
                background: $white;
                border-radius: 5px;

                img.img-fluid {
                    width:100%;
                }
                    div {
                        padding: 15px;

                        h2 {
                            font-size: 22px;
                            color: $blue;
                            text-decoration: none;
                        }

                        p {
                            font-size: 16px;
                            color: $black;

                            text-decoration: none;
                            font-weight: $light;
                        
                        &:hover{
                            text-decoration: none;
                            color: $black;
                        }
                        
                        &:active{
                            text-decoration: none;
                            color: $black;
                        }
                    }
                        .learnmore {
                            a {
                                color: $green;
                                margin-top: 30px;
                                bottom: 10px;
                                left: 50%;
                                transform: translateX(-50%);

                            }
                            span{
                                color: $green;
                                margin-top: 30px;
                                bottom: 10px;
                                left: 50%;
                                transform: translateX(-50%);
                            }
                        }
                    }
                }
            }

            .button-center {
                .button {
                    width: 100%;
                    height: 71px;
                    font-size: 14px;
                    text-align: center;
                 //   background: $button-gradient-green;
                    margin-top: 10px;
                    margin-bottom: 10px;
                    color: $white;
                }

                .button:hover {
                 //   background: $button-gradient-green-hover;
                }

                .button:active {
               //     background: $button-gradient-green-active;
                }
            }
        }
    }
