//----------Mobile------------->>>>>>>>>>>>
@include media-breakpoint-between(xs, sm) { 
    .info{
         padding-bottom:15px!important;
         #last-block{
                 margin-bottom: 15px;
         }
        .main-block{
            float: left;
            width: 100%;
            padding: 15px;
            .content-block{
              //  border:1px solid $black;
                min-height: 250px;
                background-color: $grey;
                text-align: center;
                padding: 0;
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
}