
//-----------Mobile Css
@include media-breakpoint-between(xs, sm) { 
    .serach-insight{


        .button-insight{
             .left-buttons{
                 float: left;
                 padding:15px;
                 .button{
                    // background: $button-gradient-green;
                     color: white;
                    width: auto;
                    margin-right: 10px;
                    margin-top: 10px;
                    i{
                        font-family: FontAwesome;
                        font-style: normal;
                        margin-left: 15px;

                    }
                 }
                 .dropdown1{
                     float: left;
                    .button{
                    }
                        ul{
                           padding: 10px;
                            list-style: none;
                            padding-left: 5px;
                            width: 300px;
                            background-color: rgba(13, 108, 98, 0.9);
                            position: absolute;
                            height: auto;
                            z-index: 2;
                            margin-left: 10px;
                            color: white;
                            border-radius: 10px;
                            left: 10%;
                            li{
                                height: 40px;
                                padding: 7px;
                                i{
                                    font-family:FontAwesome; 
                                    float: right;
                                    color: white;
                                    font-style: normal;
                                    font-size: 15px;
                                    display: inline-block;
                                     padding-left: 4px;
                                      padding-bottom: 2px;
                                    border:  2px solid $white;
                                    height: 25px;
                                    width: 25px;
                              
                            }
                           
                        }
                    }
                }
             }
             .right-buttons{
                 float: left;
                 padding:20px;
                 text-align: right;
                 input{
                     float: left;
                     width: 100px;
                     border-radius: 10px;  
                     width: 75%;  

                 }
                 .button{
                 //    background: $button-gradient-green;
                     color: white;
                     padding:10px;
                     margin-left: 10px;
                    float: left;
                 }
             }



        } 


        .news-feed{
            .button-bottom{
                margin: 0 auto;
                width: fit-content;
                .button{
                    padding: 15px;
                    margin: 10px;
                }
   
            }
         .row{
         padding: 15px;
         padding-left: 0;
         padding-right: 0;
            .news-section{
                padding:15px;
                .news-block{
                 box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;
                 padding-left: 0;
                 width: 100%;
                 float: left;
                 border-radius: 10px;
                 min-height: 300px;
                 min-width: 250px;
                 img{
                     margin-bottom: -40px;
                     width: 100%;
                 }
                 .title-block{
                     padding: 20px; padding-left: 0px;padding-bottom: 0px;
                     position: absolute;
                     left: 15px;
                     top: 4%;
                     .title{
                         font-size: 16px;
                         text-transform: uppercase;
                        font-weight: $regular;
                         background: #D8D8D8;
                         text-align: left;
                         padding-left: 15px;
                         color: $blue;
                         padding-top: 5px;
                         padding-bottom: 5px;
                         padding-right: 10px;
                     }
                 }
                 .content-block{
                     padding: 15px;
                     padding-top: 0; 
                     margin-top: 75px;
                     .headline{
                         font-size: 16px;
                         font-weight: $semi-bold;
                         color: $black;
                         text-decoration: none;
                        &:hover{
                            color: $black;
                            text-decoration: none;
                        }
                     }
                     .date{
                         color: $skyblue!important;

                     }
                     .learnmore{
                             color: $green;
                             position: absolute;
                             bottom: 15px;
                     }
                     .descripton{
                        font-weight: $light;
                        font-size: 14px;
                        margin-bottom: 30px;
                        
                        color: $black;
                        text-decoration: none;
                        &:hover{
                            color: $black;
                            text-decoration: none;
                        }
                    }
                 }
                }
        }
     }
}}


}
