@include media-breakpoint-between(xs, md) { 
    
    .producttable{
                h2{
                    color: $blue;
                    text-align: center;
                }
                table{
                    border-bottom: 2px solid #888888;
                    thead{
                        background: linear-gradient(180deg, rgba(0,35,64,1) 0%, rgba(0,79,143,1) 100%);
                        tr{
                        
                            th{
                                color: $white;
                                text-align: center;
                                padding:10px;
                                padding-left: 30px;
                                padding-right: 30px;
                                border-left: 2px solid #888888;
                                border-right: 2px solid #888888;
                                width: 25%;
                                font-weight: 400;
    
                            }
                        }
    
                    }
                    tbody{
                        tr{
                            td{
                                border-left: 2px solid #888888;
                                border-right: 2px solid #888888;
                                text-align: center;
                                a{
                                    color: $green;
                                }
                            }
                        }
                    }
    
                }
                p{
                    font-weight: $light;
                    font-size: 14px;
                    padding-left: 10px;
                    padding-right: 10px;
                    padding-top: 30px;
                }
    
    
    }
    }