

//--------------tablet Css

@include media-breakpoint-between(md, lg) {
    .twocolumncallouthome{
    background-color:#D8D8D8;
    padding-bottom: 20px;
    .row{
        padding:20px;
        .text-area{
            float: left;
            padding-top: 30px;
            padding-left: 0;
            padding-right: 0;
        
            h2{
                padding-bottom: 20px;
                font-size: 28px;
            }
            h3{
                color: $blue;
                font-size: 20px;
                padding-bottom: 10px;font-weight: $light;
            }
            p{
                padding-bottom: 10px;font-weight: $light;
            }
            a{
                color: $green;
                font-size: 16px;
            }
        }
        .callout-img{
            float: left;
            padding-top: 15px;
            padding-left: 0;
            padding-right: 0;
             img{
                width: 100%;
                height: auto;
            }
            iframe{
                min-height: 250px;
            }
        }
    }
}}
