//-----------Mobile Css
@include media-breakpoint-between(xs, sm) { 
    
    .generic-content-common{
  
        .generic-content{
            
        padding-bottom: 10px;
            h1{
                color: $blue;
                padding-top: 20px;
                padding-bottom: 10px;
                font-size: 22px;
                font-weight: $regular;
            }
            p{
                font-weight: 300;
                margin-bottom: 30px;
                font-size: 16px;
            }
            div{
                font-weight: 300;
                
                font-size: 20px;
            }
            span{
                font-weight: 300;
                margin-bottom: 30px;
                font-size: 16px;
            }
            img{
                float: unset;
                display: unset;
                max-width: 100%;
            }
            font-weight: 300;
                
                font-size: 16px;


                table{
                    text-align: center;
                    align-items: center;
                    tbody{
                        tr{
                            td{
                                border: 1px solid $black;
                                text-align: center;
                                vertical-align: middle;
                                padding: 10px;
                            }
                        }
                    }
                    thead{
                     tr{
                                 
                         th{
                             border: 1px solid $black;
                             text-align: center;
                             vertical-align: middle;
                             padding: 10px;
                         }
                     }
                    }
                }
                figcaption{
                    padding: 10px;
                }
             
        }
    }
    }
    