
.megamenu {
    clear: both;
    overflow: hidden;
    background-color: $blue;
    color: $white;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 200;
    display: none;
    margin-top: 6px;

    .container {
        /*.footer-left {
            width: 400px;
            float: left;
        }*/
        .megamenu-right {
            float: left;
            padding: 2% 0 2% 0;

            ul.megamenu-links {
                list-style: none;
                text-align: left;
                padding-left: 15px;
                > li {
                    float: left;
                    margin-right: 5%;
                    width: 15%;
                    > a {
                        text-transform: uppercase;
                        color: #F7BE00;
                        font-size: 18px;
                        font-weight: $semi-bold;
                    }

                    ul {
                        padding: 0px;
                        list-style: none;
                       
                        > li {
                            margin-bottom: 5px;
                            > a {
                                color: $white;
                                font-size: 18px;
                                font-weight: $regular;
                            }
                        }
                    }
                }
            }
        }

        .megamenu-products {
            float: left;
            display: inherit;
        
            .megamenu-product-chip {
                padding: 15px;
                .content-section{
                    background: $white;
                    border-radius: 5px;
                    padding:15px;
               .mega-menu-img{
                   min-height: 140px;
                   min-width: 150px;
                   img{
                   width: 100%;
                   height: auto;
                   }
               }
            }
                > div {
                    text-align: center;
                }

                .megamenu-productname {
                    font-size: 18px;
                    color: #003057;
                    text-align: center;
                    font-weight: $regular;
                }

                .megamenu-product-learnmore {
                    font-size: 14px;
                    color: #008065;
                    text-align: center;
                }
            }
        }
    }
}

