
@include media-breakpoint-between(xs, md) { 
    .asek-20{
        h1{
            color: $blue;
            padding-bottom: 20px;
        }
            .row{
               
                .content-body{
                    
                    padding: 15px;
                    float: left;
                    
                    p{
                        font-weight: $light;
                        font-size: 16px;
                    }
                    h2{
                        color: $blue;
                        padding-bottom: 10px;
                        text-align: left;
                        font-size: 20px;
                    }
                    ul{
                        list-style: inherit;    margin-block-start: 1em;
                        margin-block-end: 1em;
                        margin-inline-start: 0px;
                        margin-inline-end: 0px;
                        padding-inline-start: 40px;
                        li{
                            padding-bottom: 10px;
                            font-weight: $light;
                        }
                    }
                    h3{
                        color: $blue;
                    }
                    .caution{
                        font-style: italic;
                        font-weight: $semi-bold;
                    }
                    .utility-section{
                        float: right; 
                        width: 100%;   
                        padding: 15px;
                       
                        img{
                            max-width: 100%;
                            align-self: center;  
                            padding: 10px;
                            height: auto;
                        }
                        h2{
                            color: $blue;
                        }
                        a{
                            color: $teal;
                        }
                        .button{
                        background: $orange;
                        margin-bottom: 15px;
                        color: $white;
                        width: 100%;
                        height: 60px;
                        padding: 15px;
                        margin-top: 5px;
                        }
                        .featurekit-content {
                            display: flex;
                            padding: 10px;
                            float: left;
                            max-width: 100%;
                            .content-kit {
                                background: $white;
                                border-radius: 5px;
                                width: 100%;
                
                                img.img-fluid {
                                    width: 100%;
                                }
                
                                div {
                                    padding: 15px;
                
                                    h2 {
                                        font-size: 28px;
                                        color: $blue;
                                    }
                
                                    p {
                                        font-size: 18px;
                                        font-weight: $light;
                                    }
                
                                    .learnmore {
                                        a {
                                            color: $green;
                                        }
                
                                        margin-top: 30px;
                                    }
                                }
                            }
                        }
        
                }
                }
                
    
            }
    
    
    
    }
}


