
//--------------tablet Css

@include media-breakpoint-between(md, lg) {
    .leadershipgrid{
        background-image: linear-gradient(#ffffff, #eaeaea);margin-bottom: 20px;
    
        h1{
            color: $blue;
            
            padding-top: 15px;
            font-size: 22px;
            font-weight: $regular;
        }
        h4{
            color: $blue;
            font-style: italic;
            
            font-size: 18px;
        
        }
        .content{
            width: 100%;
            display: block;
            .text-p{
                width:70%; 
                display: block;
                padding-left: 15px;
                
            p{
                font-size: 18px;
                font-weight: $light;
            }
        }
        img{
            width: 28%;
            float: right;
        }
        }
    }
    .gridview{
        padding-bottom: 10px;

       .title-leader{
           font-size: 28px;
           color: $blue;
           padding: 10px;
       }
       .row{
        .grid-content{
            float: left;
            padding-top: 5px;
            padding-right: 0px;
            padding-left: 5px;
            h2{
                font-size: 24px;
                padding: 5px;
                padding-bottom: 0px;
            }
    
        .grid-img{
            position: relative;
            margin: 5px;
        }
    .text-block {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: $blue;
        color: white;
        min-height: 83px;
        background: transparentize($color: $blue, $amount: 0.5);
        text-align: center;
        padding:10px 5px;
        padding-bottom: 0;
        h2{
            font-size: 18px;
            line-height: 1;
            padding: 0;
            margin-bottom:5px;
        }
        p{
            font-size: 13px;
            line-height: 1;
            padding: 0;
            margin-bottom: 5px;
           
        }
      }
    }
    }
    }
    .leader-tablet{
        h1{
            color: $blue;
            font-size: 28px;
        }
    }
}