// Wrapper for the slide container and indicators

@media (min-width:1024px){
  .carouselExampleIndicators{
    margin-top: 15px;

  .carousel {
    position: relative;
    padding-top: 20px;
  }
  .carousel-inner {
    position: relative;
    width: 100%;
    overflow: hidden;
    border-radius: 5px;
  }
  .carousel-item {
    position: relative;
    align-items: center;
    width: 100%;
    @include transition($carousel-transition);
    backface-visibility: hidden;
    perspective: 1000px;
    min-height: 500px;

  }
  
  .carousel-item.active,
  .carousel-item-next,
  .carousel-item-prev {
    display: block;
  }
  
  .carousel-item-next,
  .carousel-item-prev {
    position: absolute;
    top: 0;
  }
  
  // CSS3 transforms when supported by the browser
  .carousel-item-next.carousel-item-left,
  .carousel-item-prev.carousel-item-right {
    transform: translateX(0);
  
    @supports (transform-style: preserve-3d) {
      transform: translate3d(0, 0, 0);
    }
  }
  
  .carousel-item-next,
  .active.carousel-item-right {
    transform: translateX(100%);
  
    @supports (transform-style: preserve-3d) {
      transform: translate3d(100%, 0, 0);
    }
  }
  
  .carousel-item-prev,
  .active.carousel-item-left {
    transform: translateX(-100%);
  
    @supports (transform-style: preserve-3d) {
      transform: translate3d(-100%, 0, 0);
    }
  }
  
  
  //
  // Left/right controls for nav
  //
  
  .carousel-control-prev,
  .carousel-control-next {
    position: absolute;
    top: 0;
    bottom: 0;
    // Use flex for alignment (1-3)
    display: flex; // 1. allow flex styles
    align-items: center; // 2. vertically center contents
    justify-content: center; // 3. horizontally center contents
    width: $carousel-control-width;
    color: $carousel-control-color;
    text-align: center;
    opacity: $carousel-control-opacity;
    // We can't have a transition here because WebKit cancels the carousel
    // animation if you trip this while in the middle of another animation.
  
    // Hover/focus state
    @include hover-focus {
      color: $carousel-control-color;
      text-decoration: none;
      outline: 0;
      opacity: .9;
    }
  }
  .carousel-control-prev {
    left: 0;
    @if $enable-gradients {
      background: linear-gradient(90deg, rgba(0, 0, 0, .25), rgba(0, 0, 0, .001));
    }
  }
  .carousel-control-next {
    right: 0;
    @if $enable-gradients {
      background: linear-gradient(270deg, rgba(0, 0, 0, .25), rgba(0, 0, 0, .001));
    }
  }
  
  // Icons for within
  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    display: inline-block;
    width: $carousel-control-icon-width;
    height: $carousel-control-icon-width;
    background: transparent no-repeat center center;
    background-size: 100% 100%;
  }
  .carousel-control-prev-icon {
    background-image: $carousel-control-prev-icon-bg;
    position: absolute;
    left: 15%;
  }
  .carousel-control-next-icon {
    background-image: $carousel-control-next-icon-bg;
    position: absolute;
    right: 15%;
  }
  
  
  // Optional indicator pips
  //
  // Add an ordered list with the following class and add a list item for each
  // slide your carousel holds.
  
  .carousel-indicators {
    position: absolute;
    right: 0;
    bottom: 10px;
    left: 0;
    z-index: 15;
    display: flex;
    justify-content: center;
    padding-left: 0; // override <ol> default
    // Use the .carousel-control's width as margin so we don't overlay those
    margin-right: $carousel-control-width;
    margin-left: $carousel-control-width;
    list-style: none;
  
    li {
      position: relative;
      flex: 0 1 auto;
      width: $carousel-indicator-width;
      height: $carousel-indicator-height;
      margin-right: $carousel-indicator-spacer;
      margin-left: $carousel-indicator-spacer;
      text-indent: -999px;
      background-color: rgba($carousel-indicator-active-bg, .5);
  
      // Use pseudo classes to increase the hit area by 10px on top and bottom.
      &::before {
        position: absolute;
        top: -10px;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 10px;
        content: "";
      }
      &::after {
        position: absolute;
        bottom: -10px;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 10px;
        content: "";
      }
    }
  
    .active {
      background-color: $carousel-indicator-active-bg;
    }
  }
  
  .hero-background-img-desktop{
    position: absolute;
    top: 0;
    width:100%;
    background-size: cover;
    bottom: 0;
    background-position: center; 
    background-repeat: no-repeat;
    border-radius: 5px;
  }
  // Optional captions
  //
  //
  
  .carousel-caption-left-center {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: $carousel-caption-color;
    text-align: left !important;
    transform: translate(15%, 15%);
    img{
      float: left;
      margin-right: 10px;
      margin-top: 10px;
    }
    h5{
        color: $yellow;
        font-size: 36px;
        font-weight: $light;
        text-align: left;
    }
    p{
      font-size: 19px;
      padding-bottom: 10px;font-weight: $light;
      text-align: left;
    }
   a{
      border: 1px solid white;
      color: white;
      padding: 10px;
      width: 150px;
      border-radius: 5px;
      margin-top: 20px;
      font-style: none;
      text-align: center;
      position: absolute;
      text-transform: uppercase;
      left: 30px;
      &:hover{
        color: $white;
      }
    }
   
  }
  .carousel-caption-left-bottom{
    bottom: 0;
    left: 0;
    width: 50%;
    position: absolute !important;
    transform: translate(15%,15%);
    z-index: 10;
    padding-top: 20px;
    padding-bottom: 20px;
    color: $carousel-caption-color;
    text-align: left !important;
    img{
      float: left;
      margin-right: 10px;
      margin-top: 10px;
    }
    h5{
        color: $yellow;
        font-size: 36px;
        font-weight: $light;
        text-align: left;
    }
    p{
      font-size: 19px;
      padding-bottom: 10px;font-weight: $light;
      text-align: left;
    }
   a{
      border: 1px solid white;
      color: white;
      padding: 10px;
      width: 150px;
      border-radius: 5px;
      margin-top: 20px;
      font-style: none;
      text-align: center;
      text-transform: uppercase;
      position: absolute;
      left: 30px;
      &:hover{
        color: $white;
      }
    }
  }
  .carousel-caption-right-center {
    position: absolute;
    width: 45%;
    right: 0;
    margin: 0 0 0 auto;
    padding-right: 25px;
    padding-top: 25px;
    color: $carousel-caption-color;
    text-align: left !important;
    transform: translate(-10%, 10%);

    img{
      float: left;
      margin-right: 10px;
      margin-top: 10px;display: none;
    }
    h5{
        color: $yellow;
        font-size: 36px;
        font-weight: $light;
        text-align: right;
    }
    p{
      font-size: 19px;
      padding-bottom: 10px;font-weight: $light;
      text-align: right;
    }
   a{
      border: 1px solid white;
      color: white;
      padding: 10px;
      width: 150px;
      border-radius: 5px;
      margin-top: 20px;
      font-style: none;
      text-align: center;
      float: right;
            text-transform: uppercase;
      &:hover{
        color: $white;
      }
    }
    
  }
  .carousel-caption-right-bottom{
    position: absolute;
    width: 45%;
    right: 0;
    bottom: 0;
    margin: 0 0 0 auto;
    padding-right: 25px;
    padding-top: 25px;
    color: $carousel-caption-color;
    text-align: left !important;
    transform: translate(-10%, -50%);

    img{
      float: left;
      margin-right: 10px;
      margin-top: 10px;
      display: none;
    }
    h5{
        color: $yellow;
        font-size: 36px;
        font-weight: $light;
        text-align: right;
    }
    p{
      font-size: 19px;
      padding-bottom: 10px;font-weight: $light;
      text-align: right;
    }
   a{
      border: 1px solid white;
      color: white;
      padding: 10px;
      width: 150px;
      border-radius: 5px;
      margin-top: 20px;
      font-style: none;
      float: right;
      text-transform: uppercase;
      text-align: center;
      &:hover{
        color: $white;
      }
    }
  }
  //-------------For Light Control
  
  .dark{
    .carousel-caption-left-bottom,.carousel-caption-left-center,.carousel-caption-right-bottom,.carousel-caption-right-center{
    img{
    }
    h5{
        color: $yellow;
    }
    p{
    }
    a{
      color: white;
      border: 1px solid $white;

      &:hover{
        color: $white;
      }
    } 
  }
    .carousel-control-prev{
      border: none;
      i{
        color: $yellow;
        font-size: 40px;
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translate(-50%, -50%);

      }
    }
     .carousel-control-next{
      border: none;
      i{
        color: $yellow;
        font-size: 40px;
        position: absolute;
        right: 10%;
        top: 50%;
        transform: translate(-50%, -50%);

      }
    }
    .mobile-hero-img{
      display: none;
      height: auto!important;

    }
    .desktop-hero-img{
      display: block;
      height: auto!important;
    }
  }
  .light{
    .carousel-caption-left-bottom,.carousel-caption-left-center,.carousel-caption-right-bottom,.carousel-caption-right-center{
      img{
     
    }
    h5{
        color: $blue;
       
    }
    p{
     
       color: $black;
    }
    a{
      border: 1px solid $green;
      color: $green;
      &:hover{
        color: $green;
      }
  }
}
  .carousel-control-prev{
    border: none;
    i{
      color: $blue;
      font-size: 40px;
      position: absolute;
      left: 10%;
      top: 50%;
     transform: translate(-50%, -50%);
    }
  }
   .carousel-control-next{
    border: none;
    i{
      color: $blue;
      font-size: 40px;
      position: absolute;
      right: 10%;
      top: 50%;
        transform: translate(-50%, -50%);
    }
  }

  .mobile-hero-img{
    display: none!important;
    height: auto!important;

  }
  .desktop-hero-img{
    display: block!important;
    height: auto!important;

  }
}


}



