
@media (min-width:1025px) and (max-width: 1367px)
{


    header {

        .container {
            /*max-width: none;
            width: 100%;*/
            .logo {
                &.mobile {
                    display: none;
                }
            }
        }


        .web {
            padding-top: 22px;
        }

        .utility {
            background: $grey;
            padding: 7px 0;

            .utility-text {
                float: left;
                display: table-cell;
                padding-left: 0;

                a , i{
                    font-style: arial;
                    font-size: 12px;
                    color: $green;
                    text-decoration: none;
                    font-weight: 500;
                    &:hover{
                        color: $green;
                    }
                    &:active{
                        color: $green;

                    }
                  
                }
                i {
                    color: $green;
                    font-family: FontAwesome;
                }
            }

            .utility-nav-wrapper {
                float: right;
                padding-right: 0;
                > ul {
                    float: left;
                    margin-bottom: 0;
                    width: auto;

                    li {
                        float: left;
                        list-style-type: none;
                        /*margin-right: 20px;*/
                        a {
                        }
                    }
                }

                .utility-nav {
                    float: right;

                    > li {
                        > a {
                            color: $darkgrey;
                            font-weight: 500;
                            font-size: 12px;
                        }
                    }

                    li:last-child:after {
                        content: '';
                        padding-right: 0;
                    }

                    li:after {
                        content: '|';
                        font-size: 15px;
                        padding-right: 22px;
                        padding-left: 22px;
                    }
                }

                .utility-language {
                    float: right;

                    > li {

                        > a {
                            color: $darkgrey;
                            font-weight: lighter;
                            font-size: 12px;
                        }
                    }

                    li:last-child:after {
                        content: '';
                        padding: 0;
                    }

                    li:after {
                        content: '|';
                        font-size: 15px;
                        padding-right: 22px;
                        padding-left: 22px;
                    }
                }
            }
        }

        .container {
            .branding {
                float: left;
                display: table-cell;
            }

            .search-panel {
                float: right;
                width: 70%;
                margin-top: 20px;


                .search-control-list {
                    padding: 0;
                    text-align: right;
                    margin-left: 0;
                    padding-top: 0%;
                    width: 100%;
                    float: right;


                    li {
                        list-style: none;
                        float: right;
                        margin-left: 1%;
                        min-width: 297px;

                        span {
                            width: 80%;
                            float: left;
                            position: relative;
                            max-width: 230px;

                            i {
                                position: absolute;
                                left: 10px;
                                font-size: 15px;
                                color: black;
                                top: 50%;
                                transform: translateY(-50%);
                            }

                            input {
                                font-size: 12px;
                                border-radius: 7px;
                                border: 2px solid #cfcfd0;
                                padding: 10px 10px 10px 25px;
                                width: 100%;
                                &:placeholder {
                                    padding-left: 1%;
                                }
                            }
                        }

                        button {
                            width: 58px;
                            margin-left: 3%;
                            float: left;
                            padding: 7px 5px;
                            background-image: $button-gradient-green;
                            border-color: #008375;
                            color: $white;
                            border-radius: 10px;
                            line-height:1.35;
                        }
                    }

                    .check-stock-li {
                        width: 36%;
                    }
                }
            }

            .nav-wrapper {
                float: right;
                clear: right;
                display: table-cell;
                width: 100%;

                .primary-nav {
                    font-size: 17px;
                    font-weight: bold;
                    float: right;
                    width: 100%;
                    text-align: right;

                    .primary-nav-list {
                        padding-left: 0;
                        width: 105%;
                        margin: 0;
                        list-style: none;
                        display: flex;
                        justify-content: space-between;
                        margin-top:15px;

                        > li {
                            vertical-align: middle;
                            padding-left: 1%;
                            width: auto;
                            display: inline-block;
                            padding-bottom : 5px;
                            font-weight: 500;

                            &:hover {
                                > div {
                                    display: block;
                                }
                            }
                            &:first-child{
                                padding-left: 0;
                            }
                            > a {
                                color: $blue;
                                text-transform: uppercase;
                                font-weight: $regular;
                            }
                        }
                    }
                }
            }
        }

        max-height: 200px;
        margin: 0 auto;
    }
  
}
