
footer {
    position: relative;
    clear: both;
    overflow: hidden;
    background-image: linear-gradient(#003057,#00203B);
    color: $white;
    .container {
        padding: 40px;
        position: relative;
        padding-bottom: 15px;

        > img {
            position: absolute;
            top: 150px;
            left: 30px;
        }

        .row {
            padding: 0;
        }

        .footer-left {
            width: 400px;
            float: left;
            padding: 0;

            .vline {
                float: left;
            }

            .footer-tag-line {
                font-size: 16px;
                padding-left: 20px;
                padding-right: 0;
                padding-top: 20px;

                p {
                    padding-top: 20px;
                    font-size: 16px;
                    font-weight: $light;
                }
            }

            ul.social-media {
                margin-left: 20px;
            }
        }

        .footer-right {
            float: left;

            ul.footer-links {
                list-style: none;

                > li {
                    float: left;
                    margin-right: 1%;
                    width: 19%;
                    line-height: 1.4;
                    font-style: normal;
                    font-weight: 600;


                    > a {
                        color: $white;
                        text-transform: uppercase;
                        font-size: 15px;
                        font-style: normal;
                    }

                    ul {
                        padding: 0px;
                        list-style: none;
                        font-style: normal;
                        margin-top: 10px;

                        > li {
                            > a {
                                color: $white;
                                font-size: 12px;
                                display: block;
                                font-style: normal;
                                margin-bottom: 5px;
                                padding-bottom: 15px;
                            }
                        }
                    }
                }
            }
        }

        .footer-bottom {
            clear: both;
            margin-top: 50px;
            margin: 0 auto;
            padding: 0;
           

            .footer-bottom-link {
                float: left;
                width: auto;
                padding-left: 12px;
                margin-top: 100px;
                margin-left: 20px;

                > li {
                    width: 100%;
                    display: inline;
                    margin-right: 20px;

                    a {
                        color: $white;
                        font-style: normal;
                        font-size: 15px;
                        font-weight: $regular;
                    }
                }
            }

            .footer-copyright {
                float: left;
            }
        }

        .margin-0 {
            padding-left: 0;
            margin: 0;

            ul.social-media {
                list-style: none;
                margin-top: 20px;


                > li {
                    a{
                        color: $white;
                        &:hover{
                            color: $white;
                        }
                        &:active{
                            color: $white;
                        }
                    i {
                        width: 40px;
                        height: 25px;
                        float: left;
                        font-size: 25px;
                    }
                }
                }
            }
        }
    }
}

.copyright-text{
    font-size: 14px;
    font-weight: $regular;
    padding-top: 4px;
    margin: 0 auto;
}

@import "mobile/footer-mobile";
