
//tablat
@include media-breakpoint-between(md, lg) {

    .contact-us{
        padding:0;
        margin:0;
        h1{
            color: $blue;
            margin-bottom: 20px;
            margin-top: 20px;
        }
        h3{
            font-weight: 300;
            margin-bottom: 20px;
        }
        .content-section{
            padding:15px;
            padding-left: 0;
            padding-right: 0;
            .field-validation-error{
                
                color: red;
                font-size: 12px;
                padding: 5px;
            }
            input{
                    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;

            }
            select{
                    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;

            }
            textarea{
                    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;

            }
            .button{
               // background: $button-gradient-green;
                height: 50px;
                width: 30%;
                color: $white;
                i{
                    font-family: "FontAwesome";
                    padding-right: 5px;
                    font-size: 20px;
                }
                 }
            .button:hover{
            //    background: $button-gradient-green-hover;
            }
            .button:active{
         //       background: $button-gradient-green-active;
            }
        }
    }

}
