
.alternate-callout1-blue-right{
    .main-secton-division{
        min-height: 500px;
        position: relative;
        padding: 40px 0;
        .left{
            background-image: url('/images/twocolumn-bluetheme.png');
            min-height: 500px;
            width: 50%;
            float: left;
            background-repeat:no-repeat;
            background-size: cover; 
            top: 0;
            bottom: 0;
            background-position: center center;

            position: absolute;
            
        }
        .right{
            top: 0;
            bottom: 0;
            width:70%;
            float:left;
             position:absolute; 
              right: 0;
              min-height: 500px;
              background-image: linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(0,0,85,0.18) 5%,rgba(0,48,80,0.36) 10%,rgba(0,48,81,1) 28%,rgba(0,47,86,1) 100%);
          
              
        }
        .content-block{
            position: relative;
            left: 0;
            right: 0;
            min-height: 500px;
            .left-1{
                position: static;
                min-height: 500px;
                width:50%;
                float:right;
             .left-1-inner{
                position: absolute;
                top:50%;
                transform: translateY(-50%);
                z-index: 20;
                color: $white;
                width: 50%;
                right: 0;
                    h2{
                        color: $white; 
                        font-size: 28px;
                        text-align: left;
                    }
                    p{
                        color: $white;
                        font-weight: $light;
                    }
                    a{
                        color: #F7BE00;
                        text-transform: uppercase;
                        font-weight: $regular;
                    }
                    ul{
                            list-style-type: disc;
                            -moz-columns: 2;
                            overflow: visible;
                            padding-left: 20px;
                            column-gap: 37px;
                            text-indent: 0;
                            
                        }
                        ul li{
                            padding-right: 20px;
                            break-inside: avoid-column;


                            a{
                                color: $white;        
                                font-weight: $light;
                                text-transform: none;
                            }
                        
                        }
                }
            }
        }

    }
}

