
//--------------Tablet------------->>>>>>>>
@include media-breakpoint-between(md, lg) {

    .imagemodelwindow{
      background-color:white;
      padding-top: 15px;
      .content-div{
          padding:10px;
          float: left;
          padding-left: 15px;
          padding-right: 15px;
          
          .content-body{
              padding:20px;
              box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;            
              border-radius: 5px;
              background-color: $white;
              h2{
                  font-size: 28px;
                  color: $blue;
                  text-align: left;
              }
              .text-expand1,.text-expand2{
                font-weight: $light;
                font-size: 14px;
              }
              a{
                  color: $green;
              }
              ul{
                list-style: inherit;
                li{
                  font-weight: $light;
                  font-size: 14px;
                }
              }
              .carousel{
                  ol{
                      li{
                          background-color: $blue;
                          width: 10px;
                          height: 10px;
                           border-radius: 100%;
                      }
                  }
              }
          .card{
              border: none;
              .collaspe{
                .card-body{
                  
                }
              }
              
              .card-header{
                  background-color: $white;
                  border-bottom: none;
                  padding:0;
                  button{
                      color: $green;
                      text-decoration: none;
                      i{
                          
                          &:before
                          {
                              font-family: fontawesome;
                              content: "\f068";
                              font-style: normal;
                              margin-right: 5px;
                          }
                      }
  
                      &.collapsed{
                        
                          i{
                              &:before
                              {
                                  font-family: fontawesome;
                                  content: "\f067";
                                  font-style: normal;
                                  margin-right: 5px;
                                  
                              }
                          }
                      }
                  }
              }
          }
          }
      }
  
  
  }
  }

  @media (max-width: 1399.98px) and (min-width: 767px){
    .carousel-indicators{
        bottom: -4%;    
    }
  }