
//-----------Mobile Css
@include media-breakpoint-between(xs, sm) { 
    .twocolumncallouthome{
        background-color: #D8D8D8;
        padding-bottom: 20px;
        padding-left: 10px;
        padding-right: 10px;
        .row{
            padding: 5px;
            .text-area{
                float: left;
                padding-left: 10px;
                 padding-right: 10px;
                h2{
                    padding-bottom: 20px;
                    padding-top: 20px;
                    text-align: left;
                    color: $black;
                    font-size: 28px;
                }
                h3{
                    color: $blue;
                    font-size: 20px;
                    padding-bottom: 10px;font-weight: $light;
                }
                p{
                    padding-bottom: 10px;font-weight: $light;
                }
                a{
                    color: $green;
                    font-size: 16px;
                }
            }
            .callout-img{
                float: left;
                padding-top: 5px;
                padding-left: 10px;
                padding-right: 10px;
                img{
                    width: 100%;
                    height: auto;
                }
                iframe{
                    min-height: 250px;
                }
            }
        }
    }
}
