
//--------------Tablet---------->>>>>>>>
@include media-breakpoint-between(md, lg) {

    .featurekit {
        padding: 20px;

        h1 {
            color: $blue;
            text-align: center;
        }

        .featurekit-content {
            display: flex;
            float: left;
            width: 100%;
            padding-bottom: 10px;
            .content-kit {
                background: $white;
                border-radius: 5px;
                width: 100%;

                img.img-fluid {
                    width: 100%;
                }

                div {
                    padding: 15px;

                    h2 {
                        font-size: 20px;
                        color: $blue;
                        text-decoration: none;
                    }

                    p {
                        font-size: 14px;
                        font-weight: $light;
                        color: $black;
                        text-decoration: none;
                    
                    &:hover{
                        text-decoration: none;
                        color: $black;
                    }
                    
                    &:active{
                        text-decoration: none;
                        color: $black;
                    }
                }
                    .learnmore {
                        margin-top: 15px;

                        a {
                            color: $green;
                            bottom: 10px;
                            left: 50%;
                            transform: translateX(-50%);

                         }
                         span{
                            color: $green;
                            bottom: 10px;
                            left: 50%;
                            transform: translateX(-50%);
                         }
                    }
                }
            }
        }

        .button-center {
            .button {
                width: 48%;
                margin: 5px;
                font-size: 14px;
                height: 60px;
           //     background: $button-gradient-green;
                margin-top: 30px;
                margin-bottom: 30px;
                color: $white;
            }

            .button:hover {
             //   background: $button-gradient-green-hover;
            }

            .button:active {
        //        background: $button-gradient-green-active;
            }
        }
    }
}

