


@media (min-width: 1368px){

    footer {
        position: relative;
        clear: both;
        overflow: hidden;
        background-image: linear-gradient(#003057,#00203B);
        color: $white;
        
        .container {
           
            .footer-left {
                width: 100%;

                .footer-tag-line {
                    margin: 0 auto;
                }
            }

            .footer-right {
                float: left;
    
                ul.footer-links {
                    list-style: none;
    
                    > li {
                        float: left;
                        margin-right: 1%;
                        width: 19%;
                        line-height: 1.4;
                        font-style: normal;
                        font-weight: 400;
    
    
                        > a {
                            color: $white;
                            text-transform: uppercase;
                            font-size: 17px;
                            font-style: normal;
                            font-weight: $regular;
                        }
    
                        ul {
                            padding: 0px;
                            list-style: none;
                            font-style: normal;
                            margin-top: 10px;
    
                            > li {
                                > a {
                                    color: $white;
                                    font-size: 15px;
                                    display: block;
                                    font-style: normal;
                                    margin-bottom: 5px;
                                    padding-bottom: 15px;
                                    font-weight: $regular;
                                }
                            }
                        }
                    }
                }
            }
    


            .branding {
                a {
                    display: table;
                    float: none;
                }
            }

            img.vline {
                float: left;
                margin: 0 15px;
            }

            .footer-bottom {
                    
                ul.footer-bottom-link {
                    width: 100%;
                    float: none;
                    position: relative;
                    list-style: none;
                   // padding-top: 200px;
                    
                    margin: 0;
                    padding-left:30px;

                    > li {
                        width: auto;
                        text-align: left;
                        margin: 0;
                        margin-right: 0px;
                        padding: 0px 20px 10px 0px;
                        display: inline-block;

                        a {
                            color: $white;
                            text-transform: uppercase;
                        }
                    }
                }

                .copy-right {
                }
            }

            .margin-0 {
                padding-left: 0;
                margin: 0 auto;

                ul.social-media {
                    display: table;
                    float: none;
                    list-style: none;
                    padding: 0px;

                    > li {
                        width: 40px;
                        height: 25px;
                        float: left;
                    }
                }
            }
        }
    }
}



@include media-breakpoint-between(xs, sm) { 
    footer {
        position: relative;
        clear: both;
        overflow: hidden;
        background-image: linear-gradient(#003057,#00203B);
        color: $white;

        .container {
            padding: 0;
            position: relative;

            > img {
                position: absolute;
                left: 25px;
                top:125px;
            }

            .footer-left {
                width: 100%;

                .footer-tag-line {
                    padding-top: 0;
                    padding-left: 17%;

                    p {
                        padding-top: 0;
                        text-align: left;
                    }
                }
            }

            .footer-right {
                display: none;
            }


            .branding {
                a {
                    margin: 0 auto;
                    display: table;
                    float: none;
                    padding: 30px 0px;
                }
            }

            img.vline {
                float: left;
                margin: 0 15px;
            }

            .footer-bottom {
                margin: 0 auto;
                padding: 25px 15px;
                .row{
                    div{
                        width: 100%;
                        ul.footer-bottom-link {
                            width: 100%;
                            float: none;
                            position: relative;
                            list-style: none;
                            text-align: center;
                            margin: 0px;
                            padding-left: 0;
                            
                            > li {
                                width: auto;
                                text-align: left;
                                padding-left: 5%;
                                display: inline-block;
                                margin-right: 0px;
        
        
                                a {
                                    color: $white;
                                    text-transform: uppercase;
                                }
                            }
                        }
                    }
                }
                p {
                    text-align: center;
                }

               

                .copyright-text {
                    text-align: center;
                    
                    font-size: 14px;
                }
            }

            .margin-0 {
                padding-left: 0;
                margin-left: 17%;

                ul.social-media {
                    display: table;
                    float: none;
                    list-style: none;
                    padding: 0px;
                    margin: 0px;

                    > li {
                        width: 40px;
                        height: 25px;
                        float: left;
                    }
                }
            }
        }
    }
    }


