
.newsroom{
    h1{
        color: $blue;
        padding-top: 20px;
        padding-bottom: 10px;
    }
    h2{
      font-weight: 400;
        font-size: 24px;
    }
    .button{
       // background: $button-gradient-green;
        align-items: center;
        height: 50px;
        margin-top: 50px;
        margin-bottom: 20px;
        width: 100%;
        font-size: 20px;
    }
    .news-section{
        padding:0;
        margin-top: 50px;
        .newsresult{
            border-bottom: 1px solid #C6C6C6;
            margin-top: 20px;
            width: 100%;
            a{
                color: $blue;
                font-weight: $regular;
                font-size: 22px;
                text-align: left;
             }
          
            h5{
                color: skyblue;
                margin-bottom: 5px;
                
            }
            p{
                font-size: 16px;
                font-weight: $light;
            }

        }
        .newsresult:last-child{
            border-bottom: none;
        }
    }
    #button1{
     //   background-image: $button-gradient-green;
        border-radius: 5px;
        color: white;
        padding: .5em;
        text-decoration: none;
        padding:10px 20px;
    
    }
}

