
.mobile-search-box{
    background-image: linear-gradient(#00203B, #003057);
    padding:40px;
    padding-left: 0px;
   
    ul{
        padding-left: 0;
        li{
            margin-bottom: 20px;
            color: $white;
            
    a{
       
        i{
            color: black;
            position: relative;
            left: 10%;
        }
        input{
            padding:10px;
            padding-left: 10%;
            width:70%;border-radius: 10px;

        }
    }
    .button{
        width: 17%;
        margin-left: 4%;
        border-radius: 10%;
        padding:4%;
        color: $white;
        background-image: $button-gradient-green;


    }
    .button:hover{
        background-image: $button-gradient-green-hover;
        color: $white;
    }

    .button:active{
        background-image: $button-gradient-green-active;
        color: $white;
    }
    .text-center{
        color:$white;
     
    }

}}}

@media screen and (min-width: 500px) and (max-width: 1100px) {
    .mobile-search-box{
        background-image: linear-gradient(#00203B, #003057);
        padding-top: 20px;
        ul{
            li{
                margin-bottom: 20px;
        a{
           
            i{
                color: $teal;
                position: relative;
                left: 5%;
            }
            input{
                padding:10px;
                padding-left: 5%;
                width:70%;
    
            }
        } 
        .button{
        width: 17%;
        margin-left: 4%;
        border-radius: 10%;
        color: $white;
        background-image: $button-gradient-green;
        padding:2%;
        }
        .button:hover{
            background-image: $button-gradient-green-hover;
            color: $white;
        }
    
        .button:active{
            background-image: $button-gradient-green-active;
            color: $white;
        }
    }}
}
}
#search-box-content{
    display: none;
}

@media (max-width :1100px){
    #search-box-content{
        display: none;
    }
}