
.product-tile{
    padding-top: 20px;
    padding-bottom: 20px;
    h2{
        color: $blue;
        text-align: center;
        padding-top: 20px;
        font-size: 36px;
        font-weight: $regular;
    }
    .row{
        .product-outer{
            float: left;
            margin: 0 auto;
            padding-bottom: 20px;
           a{
            cursor: pointer;
           }
            .product-tile-inner{
                    position: relative;
                    border-radius: 5px;
            img{
                    display: block;
                    border-radius: 5px;
            }
            .top-color{
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    background-color: #673277;
                    height: 100%;
                    width: 100%;
                    left: 0;
                    right: 0;
                    opacity: 0.7;
                    transition: .5s ease;
                    border-radius: 5px;
                    .text {
                        height: 60px;
                        width: auto;
                        color: white;
                        font-size: 36px;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        -webkit-transform: translate(-50%, -50%);
                        -ms-transform: translate(-50%, -50%);
                        transform: translate(-50%, -50%);
                        text-align: center;
                        border-top: 2px solid $white;
                        border-bottom: 2px solid $white;
                        font-weight: 600;
                        z-index: 100;
                        font-style: bold;     
                          transition: transform 10s;
                        ::before{
                           
                        }
                    }
            }
            }
        }
    }
}

