
@include media-breakpoint-up(lg) { 
    .twocolumncallouthome{
        padding-bottom: 20px;
    .row{
        padding:20px;
        .text-area{
            float: left;
            padding:60px;
            padding-left: 0; 
            h2{
                padding-bottom: 20px;
                padding-top: 50px;
                font-size: 36px;
            }
            h3{
                color: $blue;
                font-size: 24px;
                padding-bottom: 10px;
                font-weight: $light;
            }
            p{
