

//--------------tablet Css

@include media-breakpoint-between(md, lg) {

    .twocolumnbanner {
        background-color: $blue;

        .row {
            text-align: center;
            width: 100%;            
            height: 100%;
            margin: 0;


            .content-block {
                padding-top: 30px;
                display: flex;
                width:100%;
                justify-content:center;
                margin:0 auto;
                .content {
                    background-color: white;
                    padding-left: 0;
                    padding-right: 0;
                        .row{
                            margin: 0 auto;
                    

                    .content-img {
                        float: left;
                        padding-right: 0;
                        padding-left: 0;

                        img {
                            float: left;
                            width: 100%;
                            height: 100%;
                            display: block;
                        }
                    }

                    .content-text {
                        padding: 30px;
                        text-align: left;

                        h2 {
                            color: $blue;
                            font-size: 20px;
                            text-align: left;
                        }

                        p {
                            font-size: 16px;
                            font-weight: $light;
                        }

                        button {
                            width: auto;
                           // background: $button-gradient-green;
                            
                            color: $white;
                            display: block;
                        }

                        ul {
                            list-style: none;
                            padding-left: 0;

                            li {
                                font-weight: $light;
                                text-align: left;

                                i {
                                    color: $green;
                                }
                            }
                        }
                    }
                }
            }
            }
            }
            .bottom-text {
                color: $white;
                font-style: italic;
                padding-top: 20px;
                padding-bottom: 10px;

                p {
                    font-size: 12px;
                }
            }
        }
    }
