
.alternate-callout1-blue-right,.alternate-callout1-blue-left{
    h2{
       // color: $white;
        text-align: center;
        padding: 15px;
        padding-left: 0;
    }
.column2{
    margin-right: 0;
    .column2-main{
        .column2-sub{
            margin-bottom: 20px;
            h3{
                color: $white;
                font-size: 26px;
                font-weight: $light;

            }
            p{
                font-size: 18px;
                color: $white!important;
                line-height: 1.2;
                margin-bottom: 5px;
            }
            a{
                font-weight: $light;
                text-transform: uppercase;

            }
        }
    }
}
.mobile{
    h2{
    //    color: $black;
        text-align: center;
        padding: 15px;
        padding-left: 0;
    }
.column2-sub{
    padding:30px;
    padding-top: 0;
    h3{
     color: $black;
     font-weight: $light;
     font-size: 24px;
    }
    p{
        font-size: 18px;
     font-weight: $light;
    }
    
    a{
        text-transform: uppercase;

    }
}
}
} 

