
@include media-breakpoint-between(xs, sm) { 
   
    .insight-slider{
    
        padding-top: 30px;
        padding-bottom: 30px;
        align-items: center;
      //  background-image: url(/images/insight-slider-bg.png);
        background-repeat: no-repeat;
        
        .container{     
            h2{
                margin-top: 5%;
                color: $blue;
                text-align: center;
                font-size: 28px;
                font-weight: $regular;
            }
        }
    }
    
        
    button.owl-prev{
        top: -5%;
        left: -5%;
        position: absolute;
        background: transparent;
        border: none;
        span{
        font-size: 36px; background: transparent;
        &:hover{
            background: transparent;
        }
        &:active{
            background: transparent;
        }
        }
        
    }
    button.owl-next{
        top:-5%;
        right: -5%;
        position: absolute;
        background: transparent;
        border: none;
        span{
            font-size: 36px; background: transparent;
            &:hover{
                background: transparent;
            }
            &:active{
                background: transparent;
            }
            }
    }
    }