
//-----------Mobile Css
@include media-breakpoint-between(xs, sm) { 
    .leadershipgrid{
        padding-right: 10px;background-image: linear-gradient(#ffffff, #eaeaea);margin-bottom: 20px;
    
        h1{
            color: $blue;
           
            padding-top: 15px;
            font-size: 18px;
        }
        h4{
            color: $blue;
            font-style: italic;
                font-size: 16px;
        
        }
        .content{
                
            img{
                padding: 10px;
            }
            p{
                padding: 15px;
                font-size: 14px;
                font-weight: $light;
            }
        }
        .leader-img{
            padding: 15px;
            float: left;
        }
    }
    .gridview{
        padding-bottom: 10px;

       .title-leader{
           font-size: 28px;
           color: $blue;
           padding: 10px;
       }
       .row{
        .grid-content{
            float: left;
            padding: 20px;
            h2{
                font-size: 24px;
                padding: 5px;
                padding-bottom: 0px;
            }
    
        .grid-img{
            position: relative;
            margin: 5px;
            img{
                width: 100%;
            }
    
        }
    .text-block {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: $blue;
        color: white;
        min-height: 90px;
        background: transparentize($color: $blue, $amount: 0.5);
        text-align: center;
        h2{
            color: $white;
        }
        p{
            font-weight: $light;
        }
      }
    }
    }
    }
    .leader-tablet{
        h1{
            color: $blue;
            font-size: 28px;
            padding: 15px;
            padding-bottom: 10px;
        }
        p{
            padding:15px;
            padding-top: 5px; font-weight: $light;
            font-size: 16px;
        }
    }
}