
.producttable{
            background: white;

            h2{
                color: $blue;
                text-align: center;
            }
            .table-scroll{

            table{
                border-bottom: 2px solid #888888;
                display: block;
                overflow-x:hidden;
                  
                thead{
                    background: linear-gradient(180deg, rgba(0,35,64,1) 0%, rgba(0,79,143,1) 100%);
                    tr{
                    
                        th{
                            color: $white;
                            text-align: center;
                            padding:10px;
                            padding-left: 30px;
                            padding-right: 30px;    
                            border-left: 2px solid #888888;
                            border-right: 2px solid #888888;
                            width: 25%;
                            font-weight: 400;
                        }
                    }

                }
                tbody{
                    tr{
                        td{
                            border-left: 2px solid #888888;
                            border-right: 2px solid #888888;
                            text-align: center;
                            a{
                                color: $green;
                            }
                        }
                    }
                }

            }
        }
            p{
                font-weight: $light;
                font-size: 18px;
                padding-left: 50px;
                padding-right: 50px;
                padding-top: 30px;
            }


}
.grey-background-table{
    background: #F8F8F8;
}
.high-grey-background-table{
    background: #F2F2F2;
}

@media (min-width:320px) and (max-width: 1367px)
{
.wrapper1, .wrapper2 {
    overflow-x: scroll;
    overflow-y:hidden;
  }
  .wrapper2 {
    overflow-x: scroll;
    overflow-y:hidden;
  }
  
  .wrapper1 {height: 20px;margin-bottom: 10px; }
  .wrapper2 {height: auto }
  
  .div1 {
    width:1050px;
    height: 20px;
  }
  
  .div2 {
    width:1050px;
    height: auto;
    overflow: auto;
  }

  .wrapper1::-webkit-scrollbar-track
{
    margin-top: 5px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
    background-color: $grey;
}

.wrapper1::-webkit-scrollbar
{
	width: 4px;

}
.wrapper1::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: $light-grey;

}


.wrapper2::-webkit-scrollbar-track
{
    margin-top: 5px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
    background-color: $grey;
}

.wrapper2::-webkit-scrollbar
{
	width: 4px;

}
.wrapper2::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: $light-grey;

}
}