
//--------------Tablet------------->>>>>>>>
@include media-breakpoint-between(md, lg) {

    .accordion-map{
        .accordion-tabs{
           
            div{
                font-weight: $light;
                font-size: 22px;
            }
            .row{
                padding-left: 15px;
                }
        
            .accordion-content{
                padding: 0;
                margin-bottom: 20px;
                box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;
                -webkit-transition-duration: 2s; /* Safari */
                transition-duration: 2s;
                .card-header{
                    border-bottom: 0;
                    padding-left: 30px;
                    padding-bottom: 0;
                    border: none!important;
                    padding-top: 5px;
                    h4{
                        font-size: 24px;
                        color: $blue;
                        font-weight:$regular;
                        width: 100%;
                        text-align: left;
                        text-decoration: none;
                        padding-bottom: 5px;
                        .accordion-btn{
                            font-size: 24px;
                            color: $blue;
                            font-weight:$regular;
                            width: 100%;
                            text-align: left;
                            text-decoration: none;
                            padding-left: 0;
                            i{
                                float: right;
                                &:before
                                {
                                    font-family: fontawesome;
                                    content: "\f068";
                                    font-style: normal;
                                    font-size: 22px;font-weight: 400;
                                }
                            }
        
                            &.collapsed{
                                i{
                                    &:before
                                    {
                                        font-family: fontawesome;
                                        content: "\f067";
                                        font-style: normal;
                                        font-size: 22px;
                                        font-weight: 400;
                                        
                                    }
                                }
                            }
                        }
                   
                    }
                }
                .collapse{
                    background-color: $grey;
                }
                .collapsing{
                    background-color: $grey;
                }
                .collapsing,.collapse{
                     .card-body{
    
    
                         padding-top: 0;
                         
                         .card-header1{
                             padding-left: 0px;
                             padding-top: 20px;
                             h5{
                              padding-left: 15px;
                                  font-size: 20px;
                              color: $blue;
                              text-align: left;
                              text-decoration: none;   
                             }
                            
                            }
                             .row{
                                 padding-left: 0;
                                 margin-right: 15px;
                                 margin-left: 15px;
                                 border-bottom: 2px dashed rgba(0,48,87,0.5);
                                 &:last-child{
                                    border-bottom: none;
                                }
                                     .map-tab-content{
                                     padding-bottom: 5px;
                                     padding-top: 10px;
                                     padding-left: 0;

                                 h4{
                                     font-size: 16px;
                                     margin-top: 10px;
                                 }
                                 h3{
                                     font-size: 14px;
                                 }
                                 p{
                                    font-size: 14px;
                                }
                             }
                            }
                            }
              }
            }
        
        
    
        }
       
    }
    .plusminus{
        float:right;
        font-size: 18px;font-weight: 400;
    }
}


@media (min-width: 767px) and (max-width: 1024px) {
    .accordion-map .accordion-tabs {
        p,div,span{
            font-size: 16px;
            font-weight: 300;
        }
        h4{
            font-size: 24px;    
        }
    }
}

