
.imagewithbutton{
    background-image: linear-gradient(#ffffff, #eaeaea);
    box-shadow: 0.1rem 0.4rem 0.3rem 0rem rgba(0, 0, 0, 0.15);
    -moz-box-shadow:    0.1rem 0.4rem 0.3rem 0rem rgba(0, 0, 0, 0.15);
    -webkit-box-shadow: 0.1rem 0.4rem 0.3rem 0rem rgba(0, 0, 0, 0.15);
    .main-section{
            padding-left: 0;
        .img-part{
            padding:20px;
            img{
                height:100%;
                width: 100%;
            }
        }
        .button-part{
            padding:20px;
            margin-top: 20px;
            .button{
            display: block;
            width: fit-content;
            background: $orange1;
            margin-bottom: 10px;
            border-radius: 7px;
            color: white;
           font-size: 18px;
           margin-right: 0;
           padding: 15px;
           padding-left: 70px;
           padding-right: 70px;
           i{
               padding-right: 5px;
           }
            }
        }

    }
}

@media (max-width:1024px){
    .imagewithbutton .main-section .button-part .button{
        font-size: 16px;
    }
}



