

//-----------Mobile Css
@include media-breakpoint-between(xs, sm) { 

    .twocolumnbanner {
        background-color: $blue;

        .content-block {
            padding-top: 10px;
            display: flex;
            padding-bottom: 15px;

            .content {
                background-color: white;
                padding-left: 0;
                padding-right: 0;

                .content-img {
                    float: left;

                    img {
                        float: left;
                        width: 100%;
                        height: 150px;
                        display: block;
                    }
                }

                .content-text {
                    padding: 20px;
                    padding-left: 30px;
                    text-align: left;

                    h2 {
                        color: $blue;
                        font-size: 20px;
                        text-align: left;
                    }

                    p {
                        font-size: 16px;
                        font-weight: $light;
                    }
                    img{
                        width: 100%!important;
                    }

                    button {
                        width: 100%;
                     //   background: $button-gradient-green;
                        color: $white;
                    }

                    ul {
                        list-style: none;
                        padding-left: 0;

                        li {
                            font-weight: $light;

                            i {
                                color: $green;
                            }
                        }
                    }
                }
            }
        }

        .bottom-text {
            color: $white;
            font-style: italic;
            padding: 10px;

            p {
                font-size: 12px;
            }
        }
    }
}

