

//mobile
@include media-breakpoint-between(xs, sm) { 
    .contact-us{
        h1{
            color: $blue;
            margin-bottom: 15px;
            margin-top: 10px;
        }
        h3{
            font-weight: 300;
            margin-bottom: 15px;
        }
        
        .content-section{
            padding:0;
            .field-validation-error{
                
                color: red;
                font-size: 12px;
                padding: 5px;
            }
            input{
                margin-top: 10px;
                margin-bottom: 10px;  
                box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;

                
            }
            select{
                margin-top: 10px;
                margin-bottom: 10px;
                box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;

                
            }
            textarea{
                margin-top: 20px;
                margin-bottom: 10px;
                height: 300px;
                    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15) !important;

            }
            .button{
             //   background: $button-gradient-green;
                width: 100%;
                margin-top: 10px;
                margin-bottom: 10px;
                height: 50px;
                color: $white;
                i{
                    font-family: "FontAwesome";
                    padding-right: 5px;
                    font-size: 20px;
                }
            }
            .button:hover{
         //       background: $button-gradient-green-hover;
            }
            .button:active{
         //       background: $button-gradient-green-active;
            }
    
        }
    }
}
