
.alternatecallout-homepage {
    display: block;
    width: 100%;
    position: relative;

    .alternatecallout-main {
        position: relative;
        display: block;
        margin-left: 0;
        margin-right: 0;

      
        .alternatecallout-imgdiv {
            width: 60%;
            background-image: url('/images/twocolumn-bluetheme.png');
            /*min-height: 500px;*/
            padding-top: 26%;
            background-position: center;
            background-size: 100% auto;
            background-repeat: no-repeat;
        }

        .alternatecallout-contentdiv {
            width: 40%;
            z-index: 10;
            position: absolute;
            transform: translate(-10%, -50%);
            top: 45%;


            > div {
                width: 100%;
             

                > h2 {
                    font-weight: $regular;
                    padding-left: 15px;
                    
                }

                > p {
                    line-height: 1.2;
                }

                > a {
                    font-size: 16px;
                    color: #F7BE00;
                    font-weight: $regular;
                    text-transform: uppercase;

                }
            }
        }

        .alternatecallout-gradient {
            position: absolute;
            /*top: 0;
            left: 44%;*/
            z-index: 10;
            /*right: 0px;*/
            /*bottom: -177px;*/
            width: 60%;
            height: 100%;
        }

        &.img-left {
            .alternatecallout-imgdiv {
                float: left;
            }

            .alternatecallout-gradient {
                right: 0;
                left: auto;
            }

            .alternatecallout-contentdiv {
                color: $white;
                float: right;
                right: 0;
                transform: translate(0%, -50%);
            }

            &.blue {
                .alternatecallout-gradient {
                    background-image: linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(0,0,85,0.18) 5%,rgba(0,48,80,0.36) 10%,rgba(0,48,81,1) 28%,rgba(0,47,86,1) 100%);
                    /*linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(0,0,85,0.25) 6%,rgba(0,48,80,0.42) 10%,rgba(0,40,81,0.46) 11%,rgba(0,41,90,0.58) 14%,rgba(0,49,88,0.71) 17%,rgba(0,44,89,0.75) 18%,rgba(0,48,87,0.83) 20%,rgba(0,48,87,1) 24%,rgba(0,47,86,1) 100%);*/
                }
            }

            &.white {
                .alternatecallout-gradient {
                    //background-image: linear-gradient(to right, red, yellow);
                    background-image: linear-gradient(to right, rgba(247,247,247,1) 0%,rgba(247,247,247,1) 19%,rgba(247,247,247,1) 49%,rgba(247,247,247,1) 75%,rgba(247,247,247,0) 100%);
                }
            }
        }

        &.img-right {
            .alternatecallout-imgdiv {
                float: right;
            }

            .alternatecallout-gradient {
                right: auto;
                left: 0;
            }

            .alternatecallout-contentdiv {
                left: 6%;
                color: $black;
                float: left;
            }

            &.blue {
                .alternatecallout-gradient {
                    background-image: linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(0,0,85,0.18) 5%,rgba(0,48,80,0.36) 10%,rgba(0,48,81,1) 28%,rgba(0,47,86,1) 100%);
                }
            }

            &.white {
                .alternatecallout-gradient {
                    background-image: linear-gradient(to right, rgba(247,247,247,1) 0%,rgba(247,247,247,1) 19%,rgba(247,247,247,1) 49%,rgba(247,247,247,1) 75%,rgba(247,247,247,0) 100%);
                }
            }
        }
    }
}

@import "mobile/alternatecallout-mobile";
.color-green-learn{
    color: $teal!important;
}
